<template>
    <div>
        <div class="content1">
            <div class="userWrap">
                <i
                        class="el-icon-arrow-left back"
                        @click="pageStateSwitch('list','')"
                />
                <img
                        src="../../assets/imgs/img-man2.png"
                        alt=""
                >
                <div class="userInfo">
                    <el-row>
                        <el-col :span="20">
                            <div class="userInfoList">
                                <p>
                                    用户:<span>{{ currentUser.name }}</span><span class="ml">
{{ currentUser.gender }}
</span><span
                                        class="ml"
                                >
{{ currentUser.age }}岁
</span>
                                </p>
                                <p>照护类型:<span>半自理</span></p>
                                <p>身份证号:<span>{{ currentUser.id }}</span></p>
                                <p>登记时间:<span>{{ currentUser.createtime }}</span></p>
                            </div>
                            <div class="userInfoList">
                                <p>家庭住址:<span>{{ currentUser.address }}</span></p>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div
                                    class="tz-info"
                                    @click="pageStateSwitch('signInfo','')"
                            >
                                <img
                                        src="../../assets/imgs/img-tzxx.png"
                                        alt=""
                                >
                                <h5>体征信息</h5>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <div style="display: block;float:left;margin-top: 15px;margin-left: 15px">
                    <el-button
                            type="danger"
                            size="mini"
                    >
                        删除
                    </el-button>
                    <el-button
                            type="primary"
                            size="mini"
                    >
                        修改
                    </el-button>
                </div>
            </div>


            <div class="mensInfo">
                <h6>测量信息</h6>
                <el-row :gutter="20">
                    <el-col :span="25">
                        <div class="mensWrap">
                            <div class="mens-header mensBg1">
                                <img src="../../assets/imgs/icon-xueya.png">
                                <p>血压</p>
                            </div>
                            <div class="mens-con menscolor1">
                                {{ measure.blood_pressure }}
                                <!--126/79-->
                            </div>
                        </div>
                    </el-col>
                    <el-col :span="25">
                        <div class="mensWrap">
                            <div class="mens-header mensBg2">
                                <img
                                        src="../../assets/imgs/icon-xinlv.png"
                                        alt=""
                                >
                                <p>心率</p>
                            </div>
                            <div class="mens-con menscolor2">
                                <!--63-->
                                {{ measure.pulse_rate }}
                            </div>
                        </div>
                    </el-col>
                    <el-col :span="25">
                        <div class="mensWrap">
                            <div class="mens-header mensBg3">
                                <img
                                        src="../../assets/imgs/icon-smzl.png"
                                        alt=""
                                >
                                <p>睡眠质量</p>
                            </div>
                            <div class="mens-con menscolor3">
                                {{ measure.sleep_data }}

                                <!--重度睡眠-->
                            </div>
                        </div>
                    </el-col>
                    <el-col :span="25">
                        <div class="mensWrap">
                            <div class="mens-header mensBg4">
                                <img
                                        src="../../assets/imgs/icon-jibu.png"
                                        alt=""
                                >
                                <p>计步</p>
                            </div>
                            <div class="mens-con menscolor4">
                                {{ measure.step_count }}
                            </div>
                        </div>
                    </el-col>
                    <el-col :span="25">
                        <div class="mensWrap">
                            <div class="mens-header mensBg5">
                                <img
                                        src="../../assets/imgs/icon-jwd.png"
                                        alt=""
                                >
                                <p>
                                    最近定位
                                </p>
                            </div>
                            <div class="mens-con menscolor5">
                                <p
                                        style="overflow: hidden;text-overflow:ellipsis;white-space: nowrap; font-size: 16px"
                                        :title="recentPosition"
                                >
                                    <!-- TODO 人员类型-->
                                    <!-- {{recentPosition.length>13?recentPosition.substring(0,13)+'...':recentPosition}} -->
                                    {{ recentPosition }}
                                </p>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <div class="mensInfo">
                <h6>设备信息</h6>

                <div
                        v-for="item in deviceMap.bind"
                        :key="item.imei"
                        class="binding"
                >
                    <el-row>
                        <el-col :span="20">
                            <div class="binding-top-left">
                                <el-row :gutter="10">
                                    <el-col :span="8">
                                        <p><em>设备名称:</em><span>{{ item.deviceName }}</span></p>
                                    </el-col>
                                    <el-col :span="8">
                                        <p><em>IMEI:</em><span>{{ item.imei }}</span></p>
                                    </el-col>
                                    <el-col :span="8" v-if="item.GPS==='1'">
                                        <p><em>最近一次位置信息:</em><span> {{item.location}}</span></p>
                                    </el-col>
                                    <el-col :span="8">
                                        <p><em>绑定时间:</em><span>{{item.bindTime}}</span></p>
                                    </el-col>
                                    <el-col :span="8">
                                        <p><em>运行状态:</em><span>正常</span></p>
                                    </el-col>
                                    <el-col :span="8">
                                        <p><em>当前电量:</em><span
                                                v-if="item.remainingPower">{{item.remainingPower}}%</span></p>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="binding-top-right" @click="handleRemove(item.deviceName,item.imei,item)">
                                <img src="../../assets/imgs/icon-jcbd.png">解除绑定
                            </div>
                        </el-col>
                    </el-row>
                    <div class="binding-btm">
                        <el-row>
                            <el-col :span="6">
                                <div
                                        class="pointer"
                                        @click="showDetail(item)"
                                >
                                    <img src="../../assets/imgs/icon-ckxq.png">
                                    <span>查看详情</span>
                                </div>
                            </el-col>
                            <el-col :span="6">
                                <!-- TODO 传输围栏ID，展示特定围栏信息 -->
                                <div
                                        class="pointer"
                                        @click="pageStateSwitch('fenceManage','')"
                                >
                                    <img src="../../assets/imgs/icon-wlgl2.png">
                                    <span>围栏管理</span>
                                </div>
                            </el-col>
                            <el-col :span="6">
                                <!-- TODO 传输运动轨迹ID，展示特定运动轨迹信息 -->
                                <div
                                        class="pointer"
                                        @click="pageStateSwitch('trackManage','')"
                                >
                                    <img src="../../assets/imgs/icon-sbgj.png">
                                    <span>轨迹管理</span>
                                </div>
                            </el-col>
                            <el-col :span="6">
                                <div
                                        class="pointer"
                                        @click="pageStateSwitch('alarmManage',currentUser)"
                                >
                                    <img src="../../assets/imgs/icon-sbbj.png">
                                    <span>报警管理</span>
                                </div>
                            </el-col>
                        </el-row>
                    </div>
                </div>

                <div
                        v-for="item in deviceMap.unbound"
                        :key="item.deviceCatagoryCode"
                        class="noBinding"
                >
                    <el-row>
                        <el-col :span="12">
                            <p class="noBinding-left">
                                设备类型:<span>{{ item.deviceCatagoryName }}</span>
                            </p>
                        </el-col>
                        <el-col :span="6">
                            <img
                                    src="../../assets/imgs/icon-ckxq2.png"
                                    alt=""
                            >
                            <span>查看详情</span>
                        </el-col>
                        <el-col :span="6">
                            <img
                                    src="../../assets/imgs/icon-bd.png"
                                    alt=""
                            >
                            <span
                                    @click="dialogFormVisible = true
                            openBind(item.deviceCatagoryName,item.deviceCatagoryCode)"
                            >设备绑定</span>
                        </el-col>
                    </el-row>

                    <el-dialog title="设备绑定" :visible.sync="dialogFormVisible" :data-id='item'>
                        <el-form :inline="true" :model="formInline" class="demo-form-inline">
                            <el-form-item label="设备大类" style="margin-left: -300px;">
                                <el-input v-model="form.name" auto-complete="off" style="outline: none;width: 220px"
                                          :disabled="true"></el-input>
                            </el-form-item>
                        </el-form>
                        <el-form :inline="true" :model="formInline" class="demo-form-inline">
                            <el-form-item label="设备">
                                <el-select v-model="form.region" placeholder="请选择设备" @change='getEmei'>
                                    <el-option :label="item.sbmc" :value="item.sbmc"
                                               v-for='(item,index) in form.equipmentName' :key='index'></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="IMEI">
                                <el-select v-model="form.region1" placeholder="请选择IMEI">
                                    <el-option :label="item.imei" :value="item.imei" v-for='(item,index) in form.imeis'
                                               :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-form>
                        <div style="color: red">您即将绑定{{currentUser.name}}老人和上方所选设备，确认操作？</div>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="dialogFormVisible = false">取 消</el-button>
                            <el-button type="primary"
                                       @click="dialogFormVisible = false;
                                       bindEquip(form.region,form.region1,form.imeis)">
                                确 定
                            </el-button>
                        </div>
                    </el-dialog>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import ElRow from "element-ui/packages/row/src/row";
    import ElCol from "element-ui/packages/col/src/col";
    import swal from 'sweetalert';
    // import Middleware from '../common/middleware.js'


    export default {
        name: "UserDes",
        components: {
            ElCol,
            ElRow
            // 'v-header': header
        },
        props: {
            currentUser: {
                type: Object,
                required: true
            }
        },
        data() {
            return {
                contentState: 'auto', // enum [auto, map]
                signInfo: ['blood_pressure', 'pulse_rate', 'step_count', 'sleep_data'], // 血压 脉搏 步数 睡眠
                measure: {
                    'blood_pressure': '',
                    'pulse_rate': '',
                    'step_count': '',
                    'sleep_data': ''
                },
                recentPosition: '',
                deviceMap: {
                    bind: [],
                    unbound: []
                },
                dialogFormVisible: false,
                formLabelWidth: '120px',
                form: {
                    name: '',
                    region: '',
                    region1: '',
                    date1: '',
                    date2: '',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: '',
                    equipmentName: [],
                    imeis: []
                },
                formInline: {
                    user: '',
                    region: ''
                },
                loading: true
            }
        },
        mounted() {
            this.queryMeasuring()
            this.queryType()
        },
        methods: {
            pageStateSwitch(e, param) {

                if (e.includes('trackManage')) {
                    this.$store.commit('updateMapTrackBack', 'user')
                }
                console.log(e);
                param && this.$emit('changeCurrentEquipment', param)
                this.$emit('changeStatus', e)
            },
            showDetail(item) {
                this.$emit('changeCurrentEquipment', item)
                this.$emit('showDetail', {})
            },
            queryMeasuring() {
                const {signInfo, currentUser, measure} = this;
                signInfo.map(v => {
                    axios(`${global.HOST}/vital_sign/latest/${v}/${currentUser.patientid}`)
                        .then(res => {
                            res.data.steps && (measure.step_count = res.data.steps)
                            res.data.pulse && (measure.pulse_rate = res.data.pulse)
                            // todo 血压高压低压暂无数据
                            res.data.systolicPressure && (measure.blood_pressure = `${res.data.lowerBound || '-'}/${res.data.upperBound || '-'}`)
                            // todo 睡眠质量部分~暂时写死
                            measure.sleep_data = '浅度睡眠'
                        })
                })
                axios(`${global.HOST}/entity/${currentUser.patientid}/location`)
                    .then(res => {
                        this.recentPosition = res.data.address;
                    })

            },
            queryType() {
                axios(`${global.HOST}/device/category`)
                    .then(res => {
                        if (res.data.length > 0) {
                            [this.deviceMap.bind, this.deviceMap.unbound] = [[], []];
                            res.data.map(v => (
                                this.currentUser.devices.filter(o => o.deviceTypeCode === v.deviceCatagoryCode && this.deviceMap.bind.push(o)).length || this.deviceMap.unbound.push(v)
                            ))
                            console.log(this.deviceMap);
                        }
                    })
            },
            handleRemove(name, imei, item) {
                swal({
                    title: "解除绑定",
                    text: `您确定解除与设备${name}(IMEI编号${imei})的绑定关系吗？`,
                    buttons: ['取消', '确定']
                })
                    .then(willDelete => {
                        if (willDelete) {
                            axios.post(`${global.HOST}/device/${imei}/unbind/${this.currentUser.patientid}?jgbm=${item.hospitalCode}&sbmc=${item.deviceName}
                            &zcdm=${item.zcdm}&sbdm=${item.sbdm}&lxbm=${item.deviceTypeCode}&lxmc=${item.deviceTypeName}&zclsh=${item.zclsh}`)
                                .then((res) => {
                                    console.log(res)
                                    res = res.data
                                    let info = "";
                                    let flag = "";
                                    if (res.code == 'F') {
                                        info = "解除绑定失败";
                                        flag = "error";
                                    } else {
                                        info = "解除绑定成功";
                                        flag = "success";
                                    }
                                    swal(info, res.message, flag)
                                        .then()
                                        .then(ok => {
                                            if (ok) {
                                                this.$parent.queryUser(1);
                                                this.openFullScreen(2000);
                                                setTimeout(() => {
                                                    this.$parent.currentUser = this.$parent.userData.filter(v => v.id === this.currentUser.id)[0]
                                                    this.queryType()
                                                }, 3000)
                                            }
                                        })
                                })

                        }
                    });
            },
            openBind(name, code) {
                // console.log(this.$store.state.mapTrackBack);
                //
                // this.$store.commit('updateMapTrackBack', 'hhhh')
                // console.log(this.$store.state.mapTrackBack);
                this.form.name = name;
                axios(`${global.HOST}/device/category/${code}`)
                    .then(res => {
                        this.form.equipmentName = res.data
                    });
            },
            getEmei(sbmc) {
                console.log(this.currentUser.name);
                axios(`${global.HOST}/device/imeis?sbmc=${sbmc}`)
                    .then(res => {
                        console.log(res);
                        this.form.imeis = res.data;
                        this.form.region1 = res.data[0];
                    })
            },
            bindEquip(sbmc, imei, item) {
                console.log(this.currentUser);
                if (sbmc !== '' && imei !== '') {
                    axios.post(`${global.HOST}/device/${item[0].imei}/bind/${this.currentUser.patientid}?jgbm=${item[0].jgbm}&sbmc=${sbmc}&zcdm=${item[0].zcdm}&sbdm=${item[0].sbdm}&lxbm=${item[0].lxbm}&lxmc=${item[0].lxmc}&zclsh=${item[0].zclsh}`)
                        .then((res) => {
                            res = res.data
                            let info = "";
                            let flag = "";
                            if (res.code === 'F') {
                                info = "绑定失败";
                                flag = "error";
                            } else {
                                info = "绑定成功";
                                flag = "success";
                            }
                            swal(info, res.message, flag)
                                .then(ok => {
                                    if (ok) {
                                        // this.$parent.currentUser

                                        this.openFullScreen(1500)
                                        axios.get(`${global.HOST}/entity/list?pageSize=10&pageNum=1&patientId=${this.currentUser.patientid}`)
                                            .then(res => this.$parent.currentUser = res.data.rows[0])
                                            .then(this.queryType)
                                            .then(this.$parent.queryUser.bind(1))

                                    }
                                })
                        });
                } else {
                    swal('提交失败', '请选择设备名称和IMEI', 'error');
                }

            },
            openFullScreen(time) {
                const loading = this.$loading({
                    lock: true,
                    text: 'Loading',
                    spinner: 'el-icon-loading',
                    background: 'rgba(0, 0, 0, 0.7)'
                });
                setTimeout(() => loading.close(), time);
            }
        },

    }

</script>

<style scoped>
