    }

    .mensWrap .menscolor2 {
        color: #dba454;
    }

    .mensWrap .mensBg3 {
        background-color: #f5dede;
    }

    .mensWrap .menscolor3 {
        color: #ef6c6c;
    }

    .mensWrap .mensBg4 {
        background-color: #f0e3f8;
    }

    .mensWrap .menscolor4 {
        color: #c28ee0;
    }

    .mensWrap .mensBg5 {
        background-color: #cbeeee;
    }

    .mensWrap .menscolor5 {
        font-size: 16px;
        color: #3fb2b2;
    }

    .mens-header {
        position: relative;
        height: 40px;
        line-height: 40px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
    }

    .mens-header img {
