<template>
    <div class="statusWrap">
        <div>
            <img
                    src="../../../src/assets/imgs/icon-zaixian-small.png"
                    alt=""
            >
            <span>在线</span>
            <span class="online">
1
</span>
        </div>
        <div>
            <img
                    src="../../../src/assets/imgs/icon-yichang-small.png"
                    alt=""
            >
            <span>异常</span>
            <span class="abnormal">
2
</span>
        </div>
        <div>
            <img
                    src="../../../src/assets/imgs/icon-baojing-small.png"
                    alt=""
            >
            <span>报警</span>
            <span class="alarm">
2
</span>
        </div>
        <div>
            <img
                    src="../../../src/assets/imgs/icon-lixian-small.png"
                    alt=""
            >
            <span>离线</span>
            <span class="offline">
13
</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "StatusTip",
        //    TODO query status count
    }
</script>

<style scoped>

    .statusWrap {
        position: absolute;
        display: inline-block;
        width: 120px;
        height: 140px;
        background: white;
        right: 20px;
        top: 120px;
        box-shadow: 0 3px 5px 2px lightgray;
        padding: 5px 0;
        border-radius: 8px;
        font-size: 14px;
    }

    .statusWrap > div {
        display: inline-block;
        height: 30px;
        width: 100%;
        position: relative;
        margin: 3px 0;
    }

    .statusWrap > img {
        margin-right: 2px;
    }

    .statusWrap > div > * {
        vertical-align: -webkit-baseline-middle;
    }

    .online {
        color: rgb(53, 206, 89);
        font-size: 16px;
    }

    .abnormal {
        color: rgb(255, 158, 34);
    }

    .alarm {
        color: rgb(255, 122, 122);
    }

    .offline {
        color: rgb(187, 187, 187);
    }

    .statusWrap > div > span:nth-child(3) {
        font-size: 18px;
        line-height: 26px;
        float: right;
        padding-right: 15px;
        display: inline-block;
        min-width: 30px;
    }
</style>



