<template>
    <div id="listWrap">
        <el-collapse
                v-for="(item,index) in list"
                :key="index"
                v-model="activeNames"
                @change="handleChange"
        >
            <el-collapse-item style="">
                <template slot="title">
                    <div class="warn-item">
                        <div class="solve">
                            <span :class="[item.dqzt==='0'?'red':'blue']">
                                {{item.dqzt==='0'?'未处理':'已处理'}}

                            </span>
                        </div>
                        <div class="icon">
                            <img
                                    src="../../assets/imgs/man.png"
                                    alt="avatar"
                                    width="30"
                                    style="vertical-align: sub;"
                            >
                            <span>{{item.xm}}</span>
                        </div>
                        <div class="deviceName">
                            <span>{{item.sbmc}}</span>
                        </div>
                        <div class="operateStatus">
                            <label>运行状态：</label>
                            <span class="red">
                                {{item.txmc}}
                            </span>
                        </div>
                        <div class="alarmTime">
                            <label>报警时间：</label>
                            <span>{{ item.ddsj }}</span>
                        </div>
                        <div class="alarmType">
                            <label>报警类型：</label>
                            <span class="blue">
                                {{ item.txmc }}
                            </span>
                        </div>
                    </div>
                </template>
                <div class="switch-output">
                    <span style="display: inline-block;width: 10%;padding-left: 30px">
                        处理内容
                    </span>
                    <el-input
                            v-model="inputData[index]"
                            placeholder="请输入内容"
                            clearable
                            size="small"
                    />
                    <div style="width: 10%;display: inline-block">
                        <el-button
                                type="primary"
                                size="small"
                                plain
                                @click="saveHandleContent(item.txlsh,index)"
                        >
                            保存
                        </el-button>
                    </div>
                </div>

                <div class="switch-info">
                    <div>
                        <label>
                            报警地址：
                        </label>
                        <span>上海市松江区</span>
                    </div>
                </div>
            </el-collapse-item>
        </el-collapse>

        <el-pagination
                background
                align="right"
                layout="prev, pager, next"
                :total="totalCount"
                @current-change="queryTable"
        />
    </div>
</template>

<script>

    import axios from 'axios'
    import Swal from 'sweetalert2'
    import Middleware from '../common/middleware.js'

    export default {
        name: "WarningTable",
        data() {
            return {
                generalState: 0,
                totalCount: 0,
                searchCondition: {
                    start: '',
                    end: '',
                    user: '',
                    equip: '',
                    status: 2, //enum :[1:'已处理',2:'未处理',0:'全部']
                },
                activeNames: ['1'],
                list: [],
                inputData: [],
                warningDescEnum: ['vital_sign', 'fence', 'sos', 'device', 'fall']

            }
        },
        watch: {},
        mounted() {
            this.triggerState();
            this.queryTable();
        },
        methods: {
            handleChange(val) {
                console.log(val);
            },
            triggerState() {

                Middleware.$on("stateChange", state => {
                    this.generalState = Number(state);
                    this.queryTable();
                });

                Middleware.$on("conditionalChange", status => {
                    this.searchCondition = status
                    if (status.time.length === 2) {
                        this.searchCondition.start = +new Date(status.time[0])
                        this.searchCondition.end = +new Date(status.time[1])
                    }
                    this.queryTable()
                });
            },
            queryTable(page = 1) {
                axios.get(`${global.HOST}/alarm/list/${this.warningDescEnum[this.generalState - 1] || 'all'}?pageNum=${page}&pageSize=10&start=${this.searchCondition.start || ''}&end=${this.searchCondition.end || ''}&status=${this.searchCondition.status}&user=${this.searchCondition.user || ''}&equip=${this.searchCondition.equip || ''}`)
                    .then(res => {
                        console.log(res);
                        this.totalCount = res.data.total;
                        this.list = res.data.rows;

                        //  fixme 缺少字段： 姓名 设备名称 运行状态 报警类型 处理内容 报警地址
                        //  TODO 返回结果有值后将inputData中插入的数据改为api返回的值
                        //  TODO 可能加入查询所有功能
                        this.inputData = []
                        this.list.map(v => {
                            this.inputData.push(v.dqbz)
                        })
                    })
            },
            saveHandleContent(id, index) {
                if (!this.inputData[index]) {
                    return Swal({
                        type: 'error',
                        title: '保存失败',
                        text: '保存内容不能为空',
                    })
                }

                axios.post(`${global.HOST}/alarm/handle/${id}`, {content: this.inputData[index]}).then(res => {
                    // console.log(res);
                    if (res.data.code === 'T') {
                        Swal({
                            type: 'success',
                            title: '成功',
                            text: '处理内容保存成功',
                        }).then(() => {
                            Middleware.$emit('initCount', {})
                            this.queryTable(1)
                        })
                        //    todo query count
                    } else {
                        Swal({
                            type: 'success',
                            title: '失败',
                            text: '处理内容保存失败',
                        })
                    }
                })

            }
        },
    }
</script>

<style scoped>
    #listWrap {
        width: 100%;
        height: 632px;
    }

    #listWrap > div {
        margin: 10px 15px;
    }

    .warn-item {
        width: 90%;
        overflow: hidden;
        display: inline-block;
        height: 50px;
    }

    .warn-item > div {
        display: inline-block;
        background: white;
        margin: 0px 30px;
    }

    .warn-item > div > label {
        color: rgb(198, 198, 198);
    }

    .warn-item .deviceName > span, .warn-item .binder > span {
        display: inline-block;
        min-width: 60px;
    }

    .red {
        color: red;
    }

    .blue {
        color: rgb(80, 171, 254);
    }

    .el-input {
        width: 75%;
    }

    .switch-output {
        height: 50px;
    }

    .switch-info > div {
        display: inline-block;
        margin-left: 30px;
    }

    .switch-info label {
        color: rgb(198, 198, 198);
    }

    .switch-info span {
        margin-left: 10px;
    }

    .el-pager .active {
        background: rgb(129, 141, 153) !important;
    }

    @media screen and (max-width: 1000px) {
        .el-input {
            width: 70%;
        }
    }

    @media screen and (max-width: 750px) {
        .el-input {
            width: 60%;
        }
    }

</style>



