<!-- 监督员分配列表组件 -->
<template>
    <div
            :id="[$parent.state.userCount?'supervisor':'cSupervisor']"
            class="box-card"
    >
        <div class="clearfix">
            <span>实时位置管理</span>


            <!--<el-checkbox v-model="checked" @change="checkDisplay" style="margin-left: 100px">显示在地图上</el-checkbox>-->
            <!--<el-button style="float: right; padding: 3px 0;font-size: 24px" type="text" @click="switchShow"><i-->
            <!--:class="displayStatus"></i></el-button>-->
        </div>

        <el-menu
                v-show="isShow"
                id="optSelect"
                :default-active="$parent.state.userCount?'monitor':'trajectory'"
                class="el-menu-demo"
                mode="horizontal"
                @select="ATabSwitch"
        >
            <el-menu-item
                    index="monitor"
                    class="top-switch"
            >
<span>
<img src="../../../src/assets/imgs/icon-ssjk.png">
</span>实时监控
            </el-menu-item>
            <el-menu-item
                    index="trajectory"
                    class="top-switch"
            >
                <img
                        src="../../../src/assets/imgs/icon-gjcx.png"
                        alt=""
                >轨迹查询
            </el-menu-item>
        </el-menu>

        <!-- 实时监控 -->
        <div
                v-show="isShow&&isMonitorTab"
                style="padding: 10px 20px"
        >
            <el-input
                    v-model="searchInfo"
                    placeholder="请输入内容"
                    class="input-with-select"
                    :clearable="isTrue"
                    size="small"
            >
                <el-button
                        slot="append"
                        icon="el-icon-search"
                        @click="keySearch"
                />
            </el-input>
        </div>

        <el-menu
                v-show="isShow&&isMonitorTab"
                default-active="all"
                class="el-menu-demo"
                mode="horizontal"
                style="width: 100%;height: 30px"
                @select="tabSwitch"
        >
            <el-menu-item
                    index="all"
                    style="width: 33%;height: 30px;line-height: 30px"
            >
                全部(<span>{{ allCount }}</span>)
            </el-menu-item>
            <el-menu-item
                    index="online"
                    style="width: 33%;height: 30px;line-height: 30px"
            >
                在线(<span>{{ onlineCount }}</span>)
            </el-menu-item>
            <el-menu-item
                    index="offline"
                    style="width: 33%;height: 30px;line-height: 30px"
            >
                离线(<span>{{ offlineCount }}</span>)
            </el-menu-item>
        </el-menu>

        <el-table
                v-show="isShow&&isMonitorTab"
                id="monitorTable"
                height="340"
                :data="tableData"
                :show-header="false"
                style="width: 100%"
                class="tableHeight"
                :cell-class-name="tableCell"
                element-loading-text="拼命加载中"
                :highlight-current-row="true"
                @row-dblclick="selectMonitor"
        >
            <el-table-column
                    prop="icon"
                    label="status icon"
                    width="40"
                    height="30"
            >
                <template slot-scope="scope">
                    <div v-html="scope.row.icon"/>
                </template>
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="username"
                    align="left"
                    width="180"
            />
            <el-table-column
                    prop="status"
                    label="状态"
            >
                <template slot-scope="scope">
                    <div v-html="scope.row.status"/>
                </template>
            </el-table-column>
        </el-table>

        <div
                v-show="isShow&&isMonitorTab"
                class="paginationWrap"
        >
            <el-pagination
                    layout="prev, pager, next"
                    :total="currentCount"
                    @current-change="checkMonitorPagination"
            />
        </div>

        <!--轨迹查询-->
        <div
                class="block"
                style="padding:3px 10px"
        >
            <el-date-picker
                    v-show="isShow&&!isMonitorTab"
                    id="timePick"
                    v-model="dataPicker"
                    align="right"
                    type="date"
                    placeholder="选择日期"
                    size="small"
                    style="width: 280px; margin: 5px 0"
                    @change="orbitKeySearch"
            />
        </div>

        <div
                v-show="isShow&&!isMonitorTab"
                style="padding:0 10px"
        >
            <el-input
                    v-model="searchOrbitInfo"
                    placeholder="请输入内容"
                    size="small"
                    class="input-with-select"
                    style="width: 280px; margin: 5px 0"
            >
                <el-button
                        slot="append"
                        icon="el-icon-search"
                        @click="orbitKeySearch"
                />
            </el-input>
        </div>

        <el-table
                v-show="isShow&&!isMonitorTab"
                id="orbitTable"
                :show-header="false"
                :data="orbitTableData"
                style="width: 100%"
                class="tableHeight"
                highlight-current-row="true"
                height="340"
                :cell-class-name="tableCell"
                element-loading-text="拼命加载中"
                :highlight-current-row="true"
                @row-dblclick="selectTrajectory"
        >
            <el-table-column
                    prop="icon"
                    label="status icon"
                    width="40"
            >
                <template slot-scope="scope">
                    <div v-html="scope.row.icon"/>
                </template>
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="username"
                    align="left"
                    width="180"
            />
            <el-table-column
                    prop="distance"
                    label="distance"
            />
        </el-table>

        <div
                v-show="isShow&&!isMonitorTab"
                class="paginationWrap"
        >
            <el-pagination
                    layout="prev, pager, next"
                    :total="currentOrbitCount"
                    @current-change="checkOrbitPagination"
            />
        </div>
    </div>
</template>

<script>
    import jsonp from 'jsonp'

    export default {
        name: "Supervisor",
        data() {
            return {
                isShow: true,
                checked: true, // show all user in map
                isTrue: true,
                isMonitorTab: true,
                dataPicker: '',
                allCount: 0,
                onlineCount: 0,
                offlineCount: 0,
                currentCount: 0,
                currentOrbitCount: 0,
                searchInfo: '',
                searchOrbitInfo: '',
                timeSecond: Math.ceil(new Date().getTime() / 1000) - 600,
                currentSearchTab: 'all', //enum [all,online, offline]
                displayStatus: 'el-icon-caret-bottom',
                tableData: [],
                orbitTableData: [],
                convert: {
                    all: 'queryAll',
                    online: 'queryOnline',
                    offline: 'queryOffline'
                }
            }
        },
        // Automatic execution
        mounted() {
            this.$nextTick().then(() => {
                this.isMonitorTab = true;
                this.$parent.state.userCount ? this.keySearch() : this.ATabSwitch('trajectory');
            })
        }
        ,
        methods: {
            tableCell() {
                return 'table-item-cell'
            }
            ,
            switchShow() {
                this.isShow = !this.isShow;
                this.displayStatus = this.displayStatus === 'el-icon-caret-bottom' ? 'el-icon-caret-left' : 'el-icon-caret-bottom';
            }
            ,
            tabSwitch(e) {
                e && (this.currentSearchTab = e);
                this[this.convert[e]]();
            }
            ,
            queryAll(page = 1) {
                jsonp('http://yingyan.baidu.com/api/v3/entity/search?query=' + this.searchInfo + '&page_index=' + page + '&page_size=10&timeStamp=' + +new Date() + '&ak=B81d9d62c6fa63a3442cd3b6c7c020b2&service_id=146248', {name: 'stevenLee'}, (err, data) => {
                    if (err) {
                        console.error(err.message);
                    } else {
                        //empty data
                        if (data.status !== 0) {
                            this.currentSearchTab === 'all' && (this.currentCount = 0);
                            return [this.allCount, this.tableData] = [0, []];
                        }

                        this.allCount = data.total;
                        if (this.currentSearchTab === 'all') {
                            this.currentCount = this.allCount;

                            this.tableData = data.entities.map(v => {
                                let isOnline = (+new Date() - +new Date(v.latest_location.loc_time * 1000)) < 6e5;
                                return {
                                    icon: isOnline ? '<img src="../../../src/assets/imgs/icon-zaixian.png" />' : '<img src="../../../src/assets/imgs/icon-yhgl.png" />',
                                    name: v.entity_desc || v.entity_name,
                                    status: isOnline ? '<span style="color:#18a32f">在线</span>' : '<span style="color:grey">离线</span>',
                                    emit: v
                                }
                            });
                        }
                        this.tableStyleInit('monitorTable');
                    }
                });
            },

            queryOnline(page = 1) {
                jsonp('http://yingyan.baidu.com/api/v3/entity/search?query=' + this.searchInfo + '&filter=active_time:' + this.timeSecond + '&page_index=' + page + '&page_size=10&timeStamp=' + +new Date() + '&ak=B81d9d62c6fa63a3442cd3b6c7c020b2&service_id=146248', null, (err, data) => {
                    if (err) {
                        console.error(err.message);
                    } else {
                        //empty data
                        if (data.status !== 0) {
                            this.currentSearchTab === 'online' && ([this.currentCount, this.tableData] = [0, []]);
                            return this.onlineCount = 0;
                        }

                        this.onlineCount = data.total;

                        if (this.currentSearchTab === 'online') {
                            this.currentCount = this.onlineCount;
                            this.tableData = data.entities.map(v =>
                                ({
                                    icon: '<img src="../../../src/assets/imgs/icon-zaixian.png" />',
                                    name: v.entity_desc || v.entity_name,
                                    emit: v,
                                    status: '在线'
                                })
                            );
                        }
                        this.tableStyleInit('monitorTable');
                    }
                });
            }
            ,

            queryOffline(page = 1) {
                jsonp('http://yingyan.baidu.com/api/v3/entity/search?query=' + this.searchInfo + '&filter=inactive_time:' + this.timeSecond + '&page_index=' + page + '&page_size=10&timeStamp=' + +new Date() + '&ak=B81d9d62c6fa63a3442cd3b6c7c020b2&service_id=146248', null, (err, data) => {
                    if (err) {
                        console.error(err.message);
                    } else {

                        if (data.status !== 0) {
                            console.log(data.message);
                            this.currentSearchTab === 'offline' && ([this.currentCount, this.tableData] = [0, []]);
                            return this.offlineCount = 0;
                        }

                        this.offlineCount = data.total;
                        if (this.currentSearchTab === 'offline') {
                            this.currentCount = this.offlineCount;
                            this.tableData = data.entities.map(v => ({
                                icon: '<img src="../../../src/assets/imgs/icon-yhgl.png" />',
                                name: v.entity_desc || v.entity_name,
                                emit: v,
                                status: '离线'
                            }));
                        }
                        this.tableStyleInit('monitorTable');
                    }
                });
            }
            ,

            queryDistance(page = 1) {
                // searchOrbitInfo
                jsonp('http://yingyan.baidu.com/api/v3/entity/search?query=' + this.searchOrbitInfo + '&page_size=10&page_index=' + page + '&timeStamp=' + +new Date() + '&ak=B81d9d62c6fa63a3442cd3b6c7c020b2&service_id=146248', null, (err, res) => {
                    if (err) {
                        console.log(err.message);
                    } else {
                        // todo 可进行轨迹查询列表的排序
                        console.log(res);
                        if (res.status !== 0) return;
                        this.orbitTableData = [];
                        this.currentOrbitCount = res.total;

                        let time = {
                            start: (this.dataPicker ? Math.ceil(new Date(this.dataPicker).getTime() / 1000) : Math.ceil(new Date().getTime() / 1000)) - 24 * 60 * 60,
                            end: this.dataPicker ? Math.ceil(new Date(this.dataPicker).getTime() / 1000) : Math.ceil(new Date().getTime() / 1000)
                        };

                        //fixme map中使用jsonp跨域，异步请求导致页面出现数据一条一条展示的效果，可以考虑优化。
                        //tip 请求时间为当日00:00时至23:59
                        res.entities.map(v => {
                            jsonp('http://yingyan.baidu.com/api/v3/track/getdistance?' +
                                'start_time=' + time.start +
                                '&end_time=' + time.end +
                                '&entity_name=' + v.entity_name +
                                '&is_processed=0' +
                                '&process_option=need_denoise=1,need_vacuate=1,need_mapmatch=0,transport_mode=driving' +
                                '&timeStamp=' + +new Date() +
                                '&ak=B81d9d62c6fa63a3442cd3b6c7c020b2' +
                                '&service_id=146248', null, (err, result) => {
                                console.log(result);

                                if (err) {
                                    console.log(err.message);
                                } else {
                                    if (+result.status !== 0) {
                                        return this.orbitTableData = [];
                                    }
                                    this.orbitTableData.push({
                                        name: v.entity_desc || v.entity_name,
                                        icon: '<i class="el-icon-location" style="color: #03cef1;"></i>',
                                        distance: result.distance.toFixed`0` + ' m',
                                        emit: v,

                                    })
                                }
                            })
                        });
                        this.tableStyleInit('orbitTable');
                    }
                });
            }
            ,

            checkMonitorPagination(e) {
                this[this.convert[this.currentSearchTab]](e);
            }
            ,

            //轨迹查询页面切换
            checkOrbitPagination(e) {
                this.queryDistance(e);
            }
            ,

            keySearch() {
                Object.values(this.convert).map(v => this[v]``);
                setInterval(this.keySearch, 6e5 - 5)
            }
            ,
            // 轨迹查询搜索
            orbitKeySearch() {
                this.queryDistance(1);
            }
            ,
            //点击查询轨迹监控信息
            ATabSwitch(e) {
                if ((e === 'monitor') && !this.isMonitorTab) {
                    this.isMonitorTab = true;
                    //check to trajectory
                    console.log('check to trajectory');
                    this.keySearch();
                }

                if ((e === 'trajectory') && this.isMonitorTab) {
                    //check to monitor
                    this.isMonitorTab = false;
                    console.log('check to monitor');
                    this.queryDistance();
                }
            }
            ,
            //双击实时监控列表行时执行，地图上定位至这一位置
            selectMonitor(e) {
                console.log(e.emit);
                this.$emit('locationPreview', e.emit)
            }
            ,
            //双击轨迹查询，预期显示运动轨迹
            selectTrajectory(e) {
                this.$emit('trajectoryPreview', {detail: e.emit, date: this.dataPicker})
            }
            ,
            // 显示所有用户位置
            checkDisplay() {
                this.$emit('displayAllUser', this.checked)
            }
            ,
            tableStyleInit(id) {
                [...document.getElementById(id).getElementsByTagName('td')].map(v => {
                    v.style.fontSize = '12px';
                    v.style.padding = '5px';
                })

            }
        }
        ,
    }

</script>

<style scoped>
    @import "../../assets/common.css";

    .clearfix {
        padding: 7px 0;
        background: rgb(12, 126, 241);
        color: white;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 320px;
    }

    #supervisor {
        position: absolute;
        z-index: 10;
        top: 100px;
        left: 115px;
        background: white;
    }

    #cSupervisor {
        position: absolute;
        z-index: 10;
        top: 20px;
        left: 30px;
        background: white;
    }

    #statusSelect > * {
        width: 33% !important;
    }

    #optSelect > * {
        width: 49.5%;
    }

    .el-table .warning-row {
        background: oldlace;
    }

    .el-table .success-row {
        background: #f0f9eb;
    }

    .tableHeight td {
        height: 35px !important;
    }

    .paginationWrap {
        width: 100%;
        padding-top: 5px;
        background: white;
    }

    #monitorTable table tbody tr td {
        height: 20px;
        padding: 5px 0;
    }

    #monitorTable .el-table td, #monitorTable .el-table th {
        padding: 7px 0 !important;
    }

    .top-switch {
        height: 35px;
        line-height: 35px;
    }

</style>



