<template>
    <div>
        <div class="classification">
            <el-row
                    :gutter="20"
                    class="sort-wrap"
                    type="flex"
                    justify="space-between"
            >
                <el-col :span="4">
                    <div
                            class="grid-content bg-purple"
                            :class="[(hoverState===1)?'active':'']"
                            @click.stop="switchState(1)"
                    >
                        <div class="imgWrap">
                            <img v-if="hoverState===1" src="../../assets/imgs/img-tzyj-hover.png">
                            <img v-else src="../../assets/imgs/img-tzyj.png">
                        </div>
                        <div class="desc">
                            体征预警
                        </div>
                        <div class="count">
                            {{ nums[0] }}
                        </div>
                    </div>
                </el-col>
                <el-col :span="4">
                    <div
                            class="grid-content bg-purple"
                            :class="[(hoverState===2)?'active':'']"
                            @click.stop="switchState(2)"
                    >
                        <div class="imgWrap">
                            <img v-if="hoverState===2" src="../../../src/assets/imgs/img-ccwl-hover.png">
                            <img v-else src="../../../src/assets/imgs/img-ccwl.png">
                        </div>
                        <div class="desc">
                            超出围栏
                        </div>
                        <div class="count">
                            {{ nums[1] }}
                        </div>
                    </div>
                </el-col>
                <el-col :span="4">
                    <div
                            class="grid-content bg-purple"
                            :class="[(hoverState===3)?'active':'']"
                            @click.stop="switchState(3)"
                    >
                        <div class="imgWrap">
                            <img v-if="hoverState===3" src="../../assets/imgs/img-SOS-hover.png">
                            <img v-else src="../../assets/imgs/img-SOS.png">
                        </div>
                        <div class="desc">
                            SOS
                        </div>
                        <div class="count">
                            {{ nums[2] }}
                        </div>
                    </div>
                </el-col>
                <el-col :span="4">
                    <div
                            class="grid-content bg-purple"
                            :class="[(hoverState===4)?'active':'']"
                            @click.stop="switchState(4)"
                    >
                        <div class="imgWrap">
                            <img v-if="hoverState===4" src="../../assets/imgs/img-sbyc-hover.png">
                            <img v-else src="../../assets/imgs/img-sbyc.png">
                        </div>
                        <div class="desc">
                            设备异常
                        </div>
                        <div class="count">
                            {{ nums[3] }}
                        </div>
                    </div>
                </el-col>
                <el-col :span="4">
                    <div
                            class="grid-content bg-purple"
                            :class="[(hoverState===5)?'active':'']"
                            @click.stop="switchState(5)"
                    >
                        <div class="imgWrap">
                            <img v-if="hoverState===5" src="../../../src/assets/imgs/img-diedao-hover.png">
                            <img v-else src="../../../src/assets/imgs/img-diedao.png">
                        </div>
                        <div class="desc">
                            跌倒
                        </div>
                        <div class="count">
                            {{ nums[4] }}
                        </div>
                    </div>
                </el-col>
            </el-row>

            <span
                    v-if="showAll"
                    class="showMore"
                    @click.stop="showMore"
            >
        更多筛选 <i
                    class="fa fa-x fa-angle-double-up"
            />
      </span>
            <span
                    v-if="!showAll"
                    class="showMore"
                    @click.stop="showMore"
            >
        更多筛选 <i
                    class="fa fa-x fa-angle-double-down"
            />
      </span>
        </div>
        <search-input v-if="showAll"/>
    </div>
</template>

<script>
    import axios from 'axios'

    import searchInput from './searchInput';
    import Middleware from '../common/middleware.js'

    export default {
        name: "EquipmentStatus",
        components: {
            'search-input': searchInput
        },
        data() {
            return {
                hoverState: 6, // enum: 1.体征预警 2.超出围栏 3.SOS 4.设备异常 5.跌倒 ,6.全部
                nums: [1, 2, 3, 4, 5],
                showAll: false,
                warningDescEnum: ['vital_sign', 'fence', 'sos', 'device', 'fall']
            }
        },
        mounted() {
            this.queryCount()
            Middleware.$on("initCount", this.queryCount)
        },
        methods: {

            showMore() {
                this.showAll = !this.showAll;
            },
            switchState(state = 1) {
                this.hoverState = state === this.hoverState ? 6 : state;
                Middleware.$emit('stateChange', this.hoverState)
            },
            queryCount() {
                this.warningDescEnum.map((v, i) => {
                    axios.get(`${global.HOST}/alarm/count/${v}`)
                        .then(res => {
                            this.nums[i] = (res.data && res.data.data) ? res.data.data : 0;
                            if (i === this.warningDescEnum.length - 1) {
                                this.nums.reverse().reverse()
                            }
                        })
                })
            }
        }
    }
</script>

<style scoped>
    .grid-content {
        border-radius: 10px;
        box-shadow: 2px 3px 10px lightgrey;
        overflow: hidden;
    }

    .grid-content:hover {
        cursor: pointer;
        box-shadow: 4px 4px 10px lightgrey;
    }

    .classification {
        width: 94%;
        margin: 15px 15px 6px;
    }

    .bg-purple {
        height: 80px;
        background: white;
        font-size: 12px;
    }

    .active {
        background: linear-gradient(90deg, rgb(68, 128, 250), rgb(86, 190, 253));
        color: white;
    }

    .bg-purple > div {
        display: inline-block;
        height: 100%;
        vertical-align: text-bottom;
    }

    .imgWrap {
        width: 25%;
    }

    .desc {
        line-height: 80px;
        width: 47%;
        text-align: center;
    }

    .count {
        width: 25%;
        float: right;
        font-size: 34px;
        line-height: 80px;
        text-align: center;
    }

    .bg-purple img {
        height: 100%;
    }

    .showMore {
        font-size: 13px;
        color: rgb(57, 156, 247);
        padding-top: 5px;
        cursor: pointer;
        user-seletct: none;
        -webkit-user-select: none;
    }

    .sort-wrap {
        margin-bottom: 10px;
    }

    .sort-wrap > div {
        min-width: 150px;
    }
</style>



