<!--suppress ALL -->
<template>
    <div>
        <!--Trajectory monitoring-->
        <v-watcher-list
                v-if="mapType==='track'"
                @locationPreview="locationPreview"
                @displayAllUser="displayAllUser"
                @trajectoryPreview="trajectoryPreview"
        />

        <v-fence-list
                v-if="mapType==='fence'"
                @displayPolygonFence="displayPolygonFence"
                @displayCircleFence="displayCircleFence"
                @createFence="createFence"
        />

        <!-- switch map function -->
        <map-switch
                v-if="state.userCount"
                @locationMapType="locationMapType"
        />

        <baidu-map
                id="map"
                :scroll-wheel-zoom="true"
                :center="mapCenter"
                :zoom="18"
                @mousemove="syncPolyline"
                @click="collectionPoints"
        >
            <bm-copyright
                    anchor="BMAP_ANCHOR_TOP_RIGHT"
                    :copyright="[{id: 1, content: 'developer ', bounds: {ne: {lng: 110, lat: 40}, sw:{lng: 0, lat: 0}}}, {id: 2, content: '<a>Steven Lee</a>'}]"
            />

            <!-- 点 -->
            <!--<bm-marker @click="infoWindowOpen"-->
            <!--v-show="showCurrentPosition"-->
            <!--:position="markerPoint"></bm-marker>-->

            <!--<bm-overlay-->
            <!--pane="labelPane"-->
            <!--:class="{sample: true, active}"-->
            <!--@draw="draw"-->
            <!--v-for="(personal,i) in allUser.data"-->
            <!--:position="markerPoint">-->
            <!--<div class="diyRemark"><span>{{drawArr[i]}}</span></div>-->
            <!--&lt;!&ndash; todo user remark UI&ndash;&gt;-->
            <!--</bm-overlay>-->

            <bm-info-window
                    id="info_window"
                    :position="markerPoint"
                    :show="showCurrentPosition"
                    style="min-width: 360px;left: 300px;"
                    :offset="{height: 10,width:-30}"
            >
                <div class="info-top">
                    <div class="itl">
                        <img
                                src="../../../src/assets/imgs/img-woman2.png"
                                alt=""
                                style="width:50px"
                        >
                    </div>
                    <div class="itm">
                        <div>
                            <span>徐美玲</span>
                        </div>
                        <div>
                            <span>智能手表1</span>
                            <span>在线</span>
                        </div>
                    </div>
                    <div class="itr">
                        <img
                                src="../../../src/assets/imgs/img-battery.png"
                                alt=""
                        >
                    </div>
                </div>
                <div class="info-row">
                    {{ user.address }}
                </div>
                <div class="info-row">
                    {{ user.time }}
                </div>
                <div class="btn-wrap">
                    <span>
<img
        src="../../../src/assets/imgs/icon-xthj.png"
        alt=""
><span>系统呼叫</span>
</span>
                    <span>
<img
        src="../../../src/assets/imgs/icon-sjgx.png"
        alt=""
><span>数据更新</span>
</span>
                </div>
            </bm-info-window>

            <bm-overlay
                    :key="i"
                    v-for="(personal,i) in allUser.data"
                    pane="labelPane"
                    :class="{sample: true, active}"
                    :position="personal"
                    @draw="draw"
            >
                <div
                        class="diyRemark"
                        @click.stop="showDetail(personal)"
                >
                    <span>{{ drawArr[i] }}</span>
                </div>
                <!-- todo user remark UI-->
            </bm-overlay>

            <!--轨迹绘制-->
            <!--<bm-driving-->
            <!--v-if="drawRoute"-->
            <!--:start="startPoint"-->
            <!--:end="endPoint"-->
            <!--:panel="false"-->
            <!--:autoViewport=false-->
            <!--/>-->
            <bm-polyline
                    :path="path"
                    stroke-color="blue"
                    v-if="drawRoute"
                    :stroke-opacity="0.5"
                    :stroke-weight="2"
            ></bm-polyline>

            <bml-lushu
                    v-if="drawRoute"
                    :path="path"
                    :icon="icon"
                    :play="play"
                    :landmarkPois="landmarkPois"
                    :rotation="true"
                    @stop="reset"
            />

            <!-- 多边形围栏 -->
            <bm-polygon
                    v-if="displayFence==='polygon'"
                    :path="polygonPath"
                    stroke-color="blue"
                    :stroke-opacity="0.5"
                    :stroke-weight="2"
            />
            <!-- 圆形围栏-->
            <bm-circle
                    v-if="displayFence==='circle'"
                    :center="circlePath.center"
                    :radius="circlePath.radius"
                    stroke-color="blue"
                    :stroke-opacity="0.5"
                    :stroke-weight="4"
            />
            <!--:editing="true" 启用线编辑-->

            <bm-polygon
                    v-if="isCreating"
                    :path="createPolygonPath"
                    stroke-color="rgb(184,37,37)"
                    :stroke-opacity="0.5"
                    :stroke-weight="4"
                    @dblclick="saveFenceConfirm"
            />
        </baidu-map>

        <user-status-tips v-if="state.userCount"/>
    </div>
</template>

<script>
    // todo invalid track

    import Vue from 'vue'
    import jsonp from 'jsonp'
    import bMap from 'vue-baidu-map'
    import mapSwitch from './switch'
    import axios from 'axios'
    import Swal from 'sweetalert2'
    // import seondary from './secondary'
    import supervisor from './supervisor'
    import fence from './fence'
    import statusTip from './statusTip'
    import {BmlLushu} from 'vue-baidu-map'
    import middleware from '../common/middleware'

    let map = Vue.use(bMap, {
        ak: 'CshbX758Cmy62zFXUX9DRt0Iqcq5rG7K'
    });

    export default {
        name: 'BMap',
        // beforeCreate() {
        //
        // },
        components: {
            BmlLushu,
            'v-watcher-list': supervisor,
            'map-switch': mapSwitch,
            'user-status-tips': statusTip,
            'v-fence-list': fence
        },
        props: {
            state: {
                type: Object
            }
        },
        data() {
            return {
                active: false,
                drawCount: 0,
                drawArr: [],

                isCreating: false,
                clickPoint: {},
                zoom: 10,
                path: [],
                landmarkPois: [],
                play: true,
                isshow: true,
                drawRoute: false,
                mapType: '',  // enum ['' equals 'gis' , 'track' . 'fence']
                startPoint: {lng: 108.874749, lat: 34.200801},
                endPoint: {lng: 108.874749, lat: 34.200801},
                showCurrentPosition: false,
                markerPoint: {lng: 108.87471, lat: 34.200732},
                mapCenter: {
                    lng: 108.955089,
                    lat: 34.27523
                },
                user: {
                    title: '',
                    desc: '',
                    status: '',
                    position: '',//定位
                    address: '',//位置
                    time: ''
                },
                allUser: {
                    show: true,
                    data: []
                },
                icon: {
                    url: 'http://api.map.baidu.com/library/LuShu/1.2/examples/car.png',
                    size: {width: 52, height: 26},
                    opts: {anchor: {width: 27, height: 13}}
                },
                createPolygonPath: [],
                createCirclePath: {
                    center: {
                        lng: '',
                        lat: ''
                    },
                    radius: ''
                },
                polygonPath: [
                    {lng: 116.412732, lat: 39.911707},
                    {lng: 116.39455, lat: 39.910932},
                    {lng: 116.403461, lat: 39.921336}
                ],
                circlePath: {
                    center: {
                        lng: 116.404,
                        lat: 39.915
                    },
                    radius: 500
                },
                displayFence: ''  // enmu: ['polygon','circle','']
            }
        },
        mounted() {
            // console.log(this.local);
            if (this.state.mapState !== 'gis') {
                this.mapType = this.state.mapState
            }

            middleware.$on("displayLocation", ({lng, lat}) => {
                // fixme
                // lng && (this.mapCenter.lng = lng)
                // lat && (this.mapCenter.lat = lat)
            })
            this.displayAllUser(true)

            console.log(this.$store.state);
        },
        methods: {
            sleep(delay) {
                return new Promise(reslove => {
                    setTimeout(reslove, delay)
                })
            },
            async draw(obj) {
                if (!this.allUser.data.length) {
                    await sleep(1000)
                }

                if (this.drawCount === 0) {
                    this.drawArr = []
                }

                let {el, BMap, map} = obj
                let {lng, lat, name, isOnline} = this.allUser.data[this.drawCount]
                // if (name) {
                this.drawArr.push(name || '侠名')
                const pixel = map.pointToOverlayPixel(new BMap.Point(lng, lat))
                el.style.left = pixel.x - 60 + 'px'
                el.style.top = pixel.y - 20 + 'px'
                this.drawCount++;
                if (this.drawCount === this.allUser.data.length) {
                    this.drawCount = 0
                }
            },

            infoWindowOpen() {
                this.show = true
            },

            locationPreview(e) {
                // 需要经纬度、人名、设备名、设备状态、时间
                this.mapCenter = this.markerPoint = {
                    lng: e.latest_location.longitude,
                    lat: e.latest_location.latitude
                }

                const isOnline = +new Date() < (+new Date(e.modify_time) + 10 * 60 * 1000);
                console.log(e);
                this.user = {
                    title: (isOnline ? '<i class="el-icon-location" style="color:#03cef1"></i>' : '<i class="el-icon-location" style="color:#989898"></i>') + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;' + e.entity_name || "",
                    desc: e.entity_desc || '',
                    status: isOnline ? '在线' : '离线',
                    position: e.latest_location.latitude + ',' + e.latest_location.longitude,// 定位 经纬度
                    address: '',// 位置


                    time: e.modify_time || ''
                }

                // position convert
                jsonp('http://api.map.baidu.com/geocoder/v2/?callback=renderReverse' +
                    '&location=' +
                    e.latest_location.latitude + ',' +
                    e.latest_location.longitude +
                    '&output=json' +
                    '&pois=1' +
                    '&ak=B81d9d62c6fa63a3442cd3b6c7c020b2', (err, res) => {
                    if (err) {
                        return err.message;
                    }
                    console.log(res);
                    this.user.address = res.result.formatted_address;
                })

                this.showCurrentPosition = true;
            },
            // 地图坐标多点显示
            displayAllUser(e) {
                this.allUser.show = e;
                this.allUser.data.length = 0;
                this.showCurrentPosition = false;
                if (!e) return;

                jsonp('http://yingyan.baidu.com/api/v3/entity/search?page_index=1' +
                    '&page_size=120' +
                    '&sortby=l' +
                    'oc_time:desc' +
                    '&timeStamp=' + +new Date +
                    '&ak=B81d9d62c6fa63a3442cd3b6c7c020b2' +
                    '&service_id=146248',
                    (err, res) => {
                        if (err) {
                            console.log(err);
                        } else {
                            this.allUser.data.length = 0;
                            console.log(res);
                            res
                            && res.entities
                            && res.entities.length > 0
                            && res.entities.map(v =>
                                this.allUser.data.push({
                                    isOnline: (+new Date() - +new Date(v.latest_location.loc_time * 1000)) < 6e5,
                                    lng: v.latest_location.longitude,
                                    lat: v.latest_location.latitude,
                                    name: v.entity_desc,
                                    entity_name: v.entity_name
                                })
                            );
                            console.log(this.allUser.data);
                            //    run draw
                        }
                    })
            },
            /**
             * 轨迹查询，预计显示运动轨迹信息
             * 查询间隔为1天，查询条数为5000
             * 因而理论上一人一天的坐标单数据应小于5000
             * */
            trajectoryPreview(e) {
                const period = 862e2;
                const end = +(e.date ? new Date(e.date) : new Date(new Date().setHours(0, 0, 0, 0))) / 1e3;

                // console.log(new Date(start * 1000));
                // console.log(new Date((start + period) * 1000));
                jsonp('http://yingyan.baidu.com/api/v3/track/gettrack' +
                    '?service_id=146248' +
                    '&entity_name=' + e.detail.entity_name +
                    '&page_size=5000' +
                    '&page_index=1' +
                    '&start_time=' + (end - period) +
                    '&end_time=' + end +
                    '&is_processed=0' +
                    '&process_option=need_denoise=1,need_vacuate=1,need_mapmatch=0,transport_mode=driving' +
                    '&timeStamp=' + +new Date +
                    '&ak=B81d9d62c6fa63a3442cd3b6c7c020b2',
                    (err, res) => {
                        console.log('query final');
                        if (err) {
                            // Most likely error is timeout
                            console.error(err);
                        } else {
                            //  获取运动点坐标数组
                            console.log(res);
                            if (res.points.length === 0) {
                                // empty data
                                this.emptyTip();
                            } else {
                                this.drawRoute = true;

                                const removeEmpty = arr => {
                                    if (res.points[0].latitude === 0 && res.points[0].longitude === 0) {
                                        res.points.shift()
                                    }
                                }

                                removeEmpty(res.points)

                                this.startPoint = {
                                    lat: res.start_point.latitude || res.points[0].lat,
                                    lng: res.start_point.longitude || res.points[0].lng
                                };
                                this.endPoint = {lat: res.end_point.latitude, lng: res.end_point.longitude};

                                this.path = res.points.map(v => ({
                                    lat: v.latitude,
                                    lng: v.longitude
                                }))

                                this.mapCenter = this.startPoint
                                console.log(this.mapCenter);
                                this.play = true;

                            }
                        }
                    });
            },
            deepFlatten(arr) {
                return [].concat(...arr.map(v => (Array.isArray(v) ? this.deepFlatten(v) : v)));
            },
            reset() {
                this.play = false
            },
            handleSearchComplete(res) {
                this.path = res.getPlan(0).getRoute(0).getPath()
                // console.log(this.path);
            },
            emptyTip() {
                this.$notify({
                    title: '警告',
                    message: '当日该用户无数据',
                    type: 'warning'
                });
            },
            locationMapType(type) {
                this.mapType = ['track', 'fence'][--type] || ''
            },
            displayPolygonFence(e) {
                console.log('polygon');
                // this.polygonPath = e.target.getPath()
                console.log(e);
                this.polygonPath.length = 0
                e.vertexes.map(v =>
                    this.polygonPath.push({
                        lat: v.latitude,
                        lng: v.longitude
                    })
                )

                this.displayFence = 'polygon'
                // move to fence
                this.mapCenter = {
                    lng: e.vertexes[0].longitude,
                    lat: e.vertexes[0].latitude
                }
            },
            displayCircleFence(e) {
                this.displayFence = 'circle'

                this.circlePath = {
                    center: {
                        lng: e.longitude,
                        lat: e.latitude
                    },
                    radius: e.radius
                }
                this.mapCenter = this.circlePath.center
            },
            createFence(e) {
                let timerInterval
                Swal({
                    // timer: 2000,
                    type: "warning",
                    title: "围栏创建规则",
                    html: "单击鼠标左键选择围栏点<br>最少需要选取三个围栏点<br>双击鼠标左键保存围栏信息<br>单击鼠标右键取消上一个选中的围栏点",
                    confirmButtonText: '我明白',
                    // onBeforeOpen: () => {
                    //     timerInterval = setInterval(() => {
                    //         document.querySelector(".swal2-confirm")
                    //             .innerHTML = Swal.getTimerLeft()
                    //     }, 100)
                    //     document.querySelector(".swal2-confirm").disabled=true;
                    //     setTimeout(()=>document.querySelector(".swal2-confirm").disabled=false,5000)
                    // },
                })

                this.createPolygonPath.length = 0;
                this.isCreating = true;
                document.oncontextmenu = e => e.preventDefault();
                document.onmousedown = e => {
                    if (e.button == 2) {
                        // console.log("right");
                        this.rightCancel()
                    } else if (e.button == 0) {
                        // console.log("left");
                    } else if (e.button == 1) {
                        // console.log("Wheel");
                    }
                }
            },
            syncPolyline(e) {
                if (this.isCreating) {
                    this.createPolygonPath.pop``
                    this.createPolygonPath.push(this.clickPoint = e.point)
                }
            },
            // 单机地图，选区围栏坐标点
            collectionPoints(e) {
                if (this.isCreating) {
                    this.createPolygonPath.push(this.clickPoint = e.point)
                }
            },
            saveFenceConfirm() {
                setTimeout(() => {
                    this.rightCancel(3)
                    if (this.createPolygonPath.length < 3) {
                        this.createPolygonPath.length = 0;
                        return swal('围栏保存失败', '请至少选择三个点才能尝试保存', 'warning')
                    } else {
                        this.isCreating = false
                    }

                    swal({
                        title: "提示?",
                        text: "是否确定保存当前围栏信息？",
                        icon: "warning",
                        buttons: true,
                        dangerMode: true,
                        buttons: ['取消', '确定']
                    })
                        .then(willSave => {
                            console.log(this.createPolygonPath);
                            let pointSet = ''
                            let monitored_person = '#allentity'
                            this.createPolygonPath.map(v => pointSet += `${v.lat},${v.lng};`)

                            let formData = new FormData();
                            formData.set('coord_type', 'bd09ll');
                            formData.set('denoise', '5');
                            formData.set('timeStamp', +new Date);
                            formData.set('vertexes', pointSet);
                            formData.set('monitored_person', monitored_person);

                            if (willSave) {
                                axios({
                                    method: 'post',
                                    url: 'http://203.110.176.181:8090/fence-server/api_create_polygon_fence',
                                    config: {headers: {'Content-Type': 'multipart/form-data'}},
                                    data: formData
                                })
                                    .then(res =>
                                        swal(res.data.message, {
                                            icon: "success",
                                        })
                                    )
                                    .then(this.queryFence)
                            } else {
                                this.isCreating = true
                            }
                        });
                }, 100)
            },
            rightCancel(frequency = 1) {
                this.createPolygonPath.pop()
                frequency > 1 && this.rightCancel(--frequency)
            },
            showDetail(e) {
                if (!e.entity_name) return
                this.mapCenter = {
                    lng: e.lng,
                    lat: e.lat
                }
                // move to map center

                // fixme Fuck database
                axios(`${global.HOST}/device/${e.entity_name}`)
                    .then(res => {
                        console.log(res)
                    })


            }
        }
    }
</script>

<style scoped>
    * {
        text-align: center;
    }

    #map {
        width: 100%;
        height: 800px;
    }

    /* baidu logo remove */
    .BMap_cpyCtrl {
        display: none;
    }

    .anchorBL {
        display: none;
    }

    .detail-key {
        font-weight: 600;
        display: inline-block;
        width: 50px;
        margin-right: 10px;
    }

    .detail-key + span {
        font-size: 14px;
    }

    .sample {
        width: 120px;
        height: 40px;
        line-height: 40px;
        overflow: hidden;
        color: #fff;
        text-align: center;
        padding: 10px;
        position: absolute;
    }

    .sample.active {
        background: rgba(0, 0, 0, 0.75);
        color: #fff;
    }

    .diyRemark {
        background-image: url('../../../src/assets/imgs/icon-zaixian-large.png');
        background-repeat: no-repeat;
        text-align: left;
        padding-left: 50px;
    }

    .diyRemark > span {
        color: black;
        margin-left: -10px;
        font-size: 14px;
        line-height: 43px;
    }

    #info_window > .info-top {
        height: 50px;
        /*border: 1px solid sandybrown;*/
        margin-top: 10px;
    }

    #info_window > .info-top > div {
        /*border: 1px solid lightgray;*/
        display: inline-block;
        height: 100%;
        vertical-align: top;
    }

    #info_window > .info-top > .itl {
        width: 70px;
    }

    #info_window > .info-top > .itl > img {
        width: 80px;
    }

    #info_window > .info-top > .itm {
        width: 200px;
        overflow: hidden;
    }

    #info_window > .info-top > .itr {
        /*border: 1px solid lightgray;*/
        display: inline-block;
        height: 100%;
    }

    #info_window > .info-row {
        color: grey;
        margin-top: 5px;
    }

    #info_window > .btn-wrap {
        width: 100%;
        height: 50px;
        margin-top: 10px;
        border-top: 1px solid lightgray;
    }

    #info_window > .btn-wrap > span {
        display: inline-block;
        width: 49%;
        text-align: center;
        line-height: 50px;
        margin-bottom: -10px;
        cursor: pointer;
    }

    #info_window > .btn-wrap > span > * {
        vertical-align: -webkit-baseline-middle;
        margin: 0 3px;
    }

    #info_window > .btn-wrap > span:nth-child(1) {
        border-right: 1px solid lightgray;
    }

</style>





