<template>
    <el-menu
            :default-active="activeIndex"
            class="el-menu-demo"
            mode="horizontal"
            active-text-color="#ffd04b"
    >
        <div class="title-logo">
            <img
                    src="../../assets/imgs/logo1.png"
                    alt="platform logo"
                    style="float: left;padding: 10px 0px;"
            >
            <div style="margin: 0;padding: 0;height: 60px;float: left;">
                <p class="title-logo-text">
                    医养结合物联网管理系统
                </p>
                <p class="title-logo-text en">
                    Internet of things management system
                </p>
            </div>
        </div>
        <div id="title_btn_wrap">
            <span
                    class="title-btn"
                    @click="checkView('monitor')"
            >
监控中心
</span>

            <span
                    class="title-btn"
                    @click="checkView('app')"
            >
GIS地图管理
</span>
            <span
                    class="title-btn"
                    @click="checkView('warning')"
            >
报警处理
</span>
            <span
                    class="title-btn"
                    @click="checkView('workspace')"
            >
工作站
</span>
            <span
                    class="title-btn"
                    @click="checkView('query')"
            >
综合查询
</span>
        </div>

        <div class="manager">
            <span>Steven Lee</span>
            <span>一病区</span>

            <!-- 下拉选择病区 -->

            <i class="el-icon-caret-bottom"/>

            <!--</span>-->

            <span>
<img
        src="../../assets/imgs/img-user.png"
        alt="avatar"
        class="avatar"
>
</span>
            <span class="icon-wrap">
                <i class="fa fa-power-off"/>
                <!--<i class="el-icon-error"></i>-->
                <i class="el-icon-bell"/>
                <i class="el-icon-setting"/>
            </span>
        </div>
    </el-menu>
</template>

<script>
    import routes from '../../routes'


    export default {
        data() {
            return {
                activeIndex: '1'
            };
        }, mounted() {
            const index = Object.keys(routes).indexOf(location.pathname)
            if (~index) {
                [...document.getElementById('title_btn_wrap').getElementsByClassName('title-btn')].map((v, i) =>
                    i === index ? v.setAttribute('class', 'title-btn active') : this.noop()
                );
            }
        },
        methods: {
            noop() {
            },
            checkView(e) {
                if (e === location.pathname || (e === '/' && location.pathname === undefined)) {
                    console.log('current click!')
                } else {
                    open(e, '_self');
                }
            }
        }

    }
</script>

<style scoped>
    @import "~font-awesome/css/font-awesome.css";

    * {
        outline: none;
    }

    body {
        margin: 0;
    }

    .el-menu-demo {
        height: 60px;
        text-align: left;
        background: linear-gradient(to right, #1857ed, #2eabf9);
        overflow: hidden;
    }

    .title-logo {
        float: left;
        height: 60px;
        line-height: 60px;
        margin-right: 50px;
        width: 306px;
        color: white;
    }

    .title-logo-text {
        /*vertical-align: super;*/
        padding: 0;
        margin: 0;
        margin-left: 20px;
        font-size: 20px;
        height: 30px;
        line-height: 40px;
    }

    .en {
        color: rgba(247, 248, 251, 0.5);
        font-size: 11px;
        line-height: 20px;
    }

    .title-logo > img {
        width: 40px;
        /*padding-top: 10px;*/
        margin-left: 15px;
        vertical-align: middle;
    }

    #title_btn_wrap {
        display: inline-block;
        font-size: 0;
    }

    .title-btn {
        color: white;
        height: 60px;
        display: inline-block;
        width: 120px;
        line-height: 60px;
        text-align: center;
        cursor: pointer;
        font-size: 16px;
    }

    .active, .title-btn:hover {
        background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), rgb(255, 255, 255, 0));
        color: white;
    }

    .manager {
        display: inline-block;
        color: white;
        float: right;
        min-width: 330px;
        height: 60px;
    }

    .manager > span {
        direction: rtl;
        vertical-align: middle;
        margin-left: 15px;
        height: 100%;
        display: inline-block;
        line-height: 60px;
        font-size: 14px;
    }

    .avatar {
        width: 36px;
        height: 36px;
        margin-top: 12px;
    }

    .manager > .icon-wrap {
        margin: 0 20px 0 10px;
    }

    .el-icon-caret-bottom {
        margin-right: -10px;
    }

    .icon-wrap > i {
        margin: 4px;
        cursor: pointer;
    }

    @media screen and (max-width: 1160px) {

        /*#app {*/
        .title-logo-text {
            display: none;
        }

        .title-logo {
            width: 50px;
        }

        /*}*/

    }
</style>



