<template>
    <div
            :id="[$parent.state.userCount?'fence':'cFence']"
            class="box-card"
    >
        <div class="clearfix">
            <span>地理围栏</span>
        </div>

        <el-menu
                v-show="isShow"
                id="optSelect"
                default-active="fence"
                class="el-menu-demo"
                mode="horizontal"
                @select="ATabSwitch"
        >
            <el-menu-item
                    index="fence"
                    class="top-switch"
            >
                <img
                        src="../../../src/assets/imgs/icon-wlgl.png"
                        alt=""
                >
                围栏管理
                <div
                        v-if="isFenceTab"
                        class="active-style"
                />
            </el-menu-item>
            <el-menu-item
                    index="warning"
                    class="top-switch"
            >
                <img
                        src="../../../src/assets/imgs/icon-gjjl.png"
                        alt=""
                >
                告警记录
                <div
                        v-if="!isFenceTab"
                        class="active-style"
                />
            </el-menu-item>
        </el-menu>

        <!-- 实时监控 -->
        <div
                v-show="isShow&&isFenceTab"
                style="padding: 10px 20px"
        >
            <el-input
                    v-model="searchInfo"
                    size="small"
                    class="searchFence"
                    :clearable="isTrue"
                    placeholder="请输入内容"
                    suffix-icon="el-icon-search"
                    @blur="queryFence"
            />

            <div
                    class="createFence"
                    @click="createFence"
            >
                <span class="el-icon-plus"/>
                创建围栏
            </div>
        </div>

        <el-table
                v-show="isShow&&isFenceTab"
                id="monitorTable"
                height="360"
                :data="fenceArr"
                :show-header="false"
                style="width: 100%"
                class="tableHeight"
                :cell-class-name="tableCell"
                element-loading-text="拼命加载中"
                :highlight-current-row="true"
                @row-dblclick="displayFence"
        >
            <el-table-column
                    prop="fence_id"
                    label="status icon"
                    width="40"
                    height="30"
            >
                <template slot-scope="scope">
                    <span><img src="../../../src/assets/imgs/icon-weilan.png"></span>
                </template>
            </el-table-column>
            <!--@cell-click="handleTableClick"-->
            <el-table-column
                    prop="fence_name"
                    label="username"
                    align="left"
                    width="190"
            />

            <el-table-column
                    dtype="edit"
                    prop="fence_id"
                    label="status icon"
                    width="41"
                    height="30"
            >
                <template slot-scope="scope">
                    <button @click="handleEdit(scope.row,scope)">
                        <img
                                src="../../../src/assets/imgs/icon-bianji.png"
                                alt=""
                        >
                    </button>
                </template>
            </el-table-column>

            <el-table-column
                    prop="fence_id"
                    label="remove"
                    width="41"
                    height="30"
            >
                <template slot-scope="scope">
                    <button @click="handleRemove(scope.row)">
                        <img
                                src="../../../src/assets/imgs/icon-delete.png"
                                alt=""
                        >
                    </button>
                </template>
            </el-table-column>
        </el-table>

        <div
                v-show="isShow&&isFenceTab"
                class="paginationWrap"
        >
            <el-pagination
                    layout="prev, pager, next"
                    :total="fenceCount"
                    @current-change="checkFencePagination"
            />
        </div>

        <!--告警记录-->
        <div
                v-show="isShow&&!isFenceTab"
                style="padding:0 10px"
        >
            <el-input
                    v-model="searchOrbitInfo"
                    placeholder="请输入内容"
                    size="small"
                    class="input-with-select"
                    style="width: 280px; margin: 5px 0"
            >
                <el-button
                        slot="append"
                        icon="el-icon-search"
                        @click="orbitKeySearch"
                />
            </el-input>
        </div>

        <div
                class="block"
                style="padding:3px 10px"
        >
            <el-date-picker
                    v-show="isShow&&!isFenceTab"
                    id="timePick"
                    v-model="dataPicker"
                    align="right"
                    type="date"
                    placeholder="选择日期"
                    size="small"
                    style="width: 280px; margin: 5px 0"
                    @change="queryWarning()"
            />
        </div>

        <el-table
                v-show="isShow&&!isFenceTab"
                id="orbitTable"
                :show-header="false"
                :data="warningData"
                style="width: 100%"
                class="tableHeight"
                highlight-current-row="true"
                height="340"
                :cell-class-name="tableCell"
                element-loading-text="拼命加载中"
                :highlight-current-row="true"
                @row-dblclick="selectTrajectory"
        >
            <el-table-column
                    prop="monitored_person_desc"
                    label="status icon"
                    width="35"
            >
                <template slot-scope="scope">
                    <img
                            src="../../assets/imgs/icon-zaixian.png"
                            alt=""
                    >
                </template>
            </el-table-column>
            <el-table-column
                    prop="monitored_person_desc"
                    label="status icon"
                    align="left"
                    width="100"
            />
            <el-table-column
                    prop="action"
                    label="action"
                    width="100"
            />
            <el-table-column
                    prop="alarm_time"
                    label="distance"
            />
        </el-table>

        <div
                v-show="isShow&&!isFenceTab"
                class="paginationWrap"
        >
            <el-pagination
                    layout="prev, pager, next"
                    :total="currentOrbitCount"
                    @current-change="checkOrbitPagination"
            />
        </div>
    </div>
</template>

<script>
    import jsonp from 'jsonp'
    import axios from 'axios'
    import moment from 'moment'
    import swal from 'sweetalert'

    export default {
        name: "Fence",
        data() {
            return {
                isShow: true,
                checked: true, // show all user in map
                isTrue: true,
                isFenceTab: true,
                dataPicker: '',
                currentCount: 0,
                currentOrbitCount: 0,
                searchInfo: '',
                searchOrbitInfo: '',
                timeSecond: Math.ceil(new Date().getTime() / 1000) - 600,
                displayStatus: 'el-icon-caret-bottom',
                tableData: [],
                warningData: [],
                fenceArr: [],
                fenceCount: 0,
                convert: {
                    all: 'queryAll'
                },
                polygonPath: [],
                warningType: {
                    exit: '出围栏',
                    enter: '入围栏'
                }
            }
        },
        mounted() {
            // entrance method
            this.queryFence();
        },
        methods: {
            tableCell({columnIndex}) {
                return columnIndex === 1 ? 'table-item-cell editable' : 'table-item-cell'
            },
            switchShow() {
                this.isShow = !this.isShow;
                this.displayStatus = this.displayStatus === 'el-icon-caret-bottom' ? 'el-icon-caret-left' : 'el-icon-caret-bottom';
            },
            queryAll() {
                this.queryFence();
            },
            queryFence(page = 1) {
                const ts = +new Date

                axios.post('http://203.110.176.181:8090/fence-server/api_get_fences_ids?timeStamp=' + ts + '&fence_name=' + this.searchInfo,).then(res => {
                    let fid = res.data.toString()

                    // WTK：围栏列表后端没有做分页
                    jsonp('http://yingyan.baidu.com/api/v3/fence/list' +
                        '?fence_ids=' + fid +
                        '&timeStamp=' + ts +
                        '&ak=B81d9d62c6fa63a3442cd3b6c7c020b2&service_id=146248'
                        , (err, res) => {
                            if (err) {
                                return console.error(err)
                            }
                            this.fenceCount = res.fences.length || 0

                            this.fenceArr = res.fences.filter((v, i) => (i >= page * 10 - 10) && (i < page * 10))

                            console.log(res);
                        })
                })
            },

            queryWarning(page = 1) {
                let start = moment(this.dataPicker || new Date).format('YYYY-MM-DD')
                let end = moment(this.dataPicker || new Date).add(1, 'day').format('YYYY-MM-DD')
                let formData = new FormData();
                formData.set('timeStamp', +new Date);
                formData.set('pageNumber', page);
                formData.set('fence_name', this.searchInfo);
                formData.set('pageSize', 10);
                formData.set('start_time', start);
                formData.set('end_time', end);
                axios({
                    method: 'post',
                    config: {headers: {'Content-Type': 'multipart/form-data'}},
                    data: formData,
                    url: 'http://203.110.176.181:8090/fence-server/api_get_alarms',

                }).then(res => {
                    console.log(res);
                    if (res.data || res.data.message === '成功') {
                        console.log(res.data.alarmContents);
                        res.data.alarmContents.map(v => {
                            v.alarm_time = moment(v.alarm_time).format('HH:MM');
                            v.action = this.warningType[v.action]
                        })
                        this.warningData = res.data.alarmContents
                    }

                })
            },

            // 分页查询告警记录
            checkOrbitPagination(e) {
                // pagination by sql
                this.queryWarning(e);
            },
            checkFencePagination(e) {
                // pagination by js filter
                this.queryFence(e)
            },

            // 按名称查询告警记录
            orbitKeySearch() {
                this.queryWarning(1);
            }
            ,
            //点击查询轨迹监控信息
            ATabSwitch(e) {
                if ((e === 'fence') && !this.isFenceTab) {
                    this.isFenceTab = true
                    //check to fence
                    console.log('check to fence');
                    this.queryFence();
                }

                if ((e === 'warning') && this.isFenceTab) {
                    this.isFenceTab = false;
                    this.queryWarning();
                }
            }
            ,
            //双击轨迹查询，预期显示运动轨迹
            selectTrajectory(e) {
                this.$emit('trajectoryPreview', {detail: e.emit, date: this.dataPicker})
            }
            ,
            // 显示所有用户位置
            checkDisplay() {
                this.$emit('displayAllUser', this.checked)
            }
            ,
            // init table style
            tableStyleInit(id) {
                [...document.getElementById(id).getElementsByTagName('td')].map(v => {
                    v.style.fontSize = '12px';
                    v.style.padding = '5px';
                })
            },
            displayFence(obj) {
                console.log(obj);
                // tip 支持两种围栏，圆与多边形
                if (obj.shape === 'circle') {
                    this.$emit('displayCircleFence', obj)
                } else if (obj.shape === 'polygon') {
                    this.$emit('displayPolygonFence', obj)
                }
            },
            handleEdit(obj, o) {

                let node = document.querySelectorAll(".editable")[o.$index]
                node = node.children[0]
                node.setAttribute('contenteditable', true)
                node.focus()
                node.addEventListener('blur', () => {
                    node.removeEventListener('blur', this.noop)
                    node.setAttribute('contenteditable', false)
                    axios.post(`http://203.110.176.181:8090/fence-server/api_update_circle_fence?fence_id=${o.row.fence_id}&fence_name=${node.innerHTML}&timeStamp=${+new Date}`)
                        .then(swal.bind({}, '成功', '围栏名称保存成功', 'success'))
                        .catch(console.error.bind({}, 'error'))
                })
            },
            handleRemove({fence_id}) {

                swal({
                    title: "确定删除?",
                    text: "围栏删除后将不可恢复!",
                    icon: "warning",
                    buttons: ['取消', '确定']
                })
                    .then(willDelete => {
                        if (willDelete) {
                            axios(`http://203.110.176.181:8090/fence-server/api_delete_fence?fence_ids=${fence_id}&timeStamp=${+new Date}`)
                                .then(swal.bind({}, '成功', '围栏删除成功', 'success'))
                                .then(this.queryFence)
                        }
                    });
            },
            createFence() {
                this.$emit('createFence', {})
            },
            noop() {

            }
        },
    }
</script>

<style scoped>
    @import "../../assets/common.css";

    .clearfix {
        padding: 7px 0;
        background: rgb(12, 126, 241);
        color: white;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 320px;
    }

    #fence {
        position: absolute;
        z-index: 10;
        top: 100px;
        left: 115px;
        background: white;
    }

    #cFence {
        position: absolute;
        z-index: 10;
        top: 20px;
        left: 30px;
        background: white;
    }

    #statusSelect > * {
        width: 33% !important;
    }

    #optSelect > * {
        width: 50%;
    }

    .el-table .warning-row {
        background: oldlace;
    }

    .el-table .success-row {
        background: #f0f9eb;
    }

    .tableHeight td {
        height: 35px !important;
    }

    .paginationWrap {
        width: 100%;
        padding-top: 5px;
        background: white;
    }

    #monitorTable table tbody tr td {
        height: 20px;
        padding: 5px 0;
    }

    #monitorTable .el-table td, #monitorTable .el-table th {
        padding: 7px 0 !important;
    }

    .top-switch {
        height: 35px;
        line-height: 35px;
        color: white;
        background: rgb(41, 146, 255);
    }

    .top-switch img {
        width: 15px;
    }

    .top-switch:hover {
        opacity: .9;
        background: rgb(41, 146, 255) !important;
        color: white !important;
    }

    .el-menu-item * {
        vertical-align: text-bottom;
    }

    .active-style {
        position: absolute;
        left: 77px;
        bottom: -2px;
        display: inline-block;
        width: 0;
        height: 0;
        border-width: 6px;
        border-style: solid;
        border-color: transparent transparent white transparent;
    }

    .is-active {
        background: rgb(41, 146, 255) !important;
        color: white !important;
    }

    .searchFence {
        float: left;
        width: 60%;
    }

    .createFence {
        float: right;
        font-size: 13px;
        display: inline-block;
        border-radius: 6px;
        padding: 5px;
        cursor: pointer;
        color: rgb(62, 175, 255);
        background: rgb(234, 245, 254);
        border: 1px solid rgb(62, 175, 255);
    }

    button {
        border: none;
        outline: none;
        background: inherit;
        width: 5px;
        text-align: left;
        margin-left: -20px;
    }


</style>



