                    deviceTotal: 0,            //设备总数
                    personName: '',            //绑定用户
                    imei: '',                   //设备唯一标示
                    deviceName: '',            //设备名称
                    onlineStates: '',            //设备在线状态
                    jgbm: '',                   //机构编码
                    startDate: '',              //开始时间
                    endDate: ''                //结束时间
                },
            }
        },
        mounted() {
            this.queryBlockNum(),
                this.queryAllDevice(),
                this.queryAllAgencies(),
                this.queryRegistPersonList(),
                this.queryDeviceList()
        },
        methods: {
            clickBlock(state, info) {
                this.totalState = state;
                this.detailName = info
            },
            queryBlockNum() {   //查询统计块数值
                axios.get(''+global.HOST+'/IntegratedQueryPage/blockNumber').then(res => {
                    this.facilities = res.data[0]
                });
            },
            queryAllDevice() {  //查询所有设备名称
                axios.get(''+global.HOST+'/IntegratedQueryPage/queryAllDevice').then(res => {
                    var sbmc = {
                        id: '',
                        text: '请选择',
                    }
                    res.data.push(sbmc);
                    this.person.deviceOption = res.data
                });
            },
            queryAllAgencies() {  //查询所有机构
                axios.get(''+global.HOST+'/IntegratedQueryPage/queryAllAgencies').then(res => {
                    var jgmc = {
                        jgbm: '',
                        jgjc: '请选择',
                    }
                    res.data.push(jgmc);
                    this.person.agenciesOption = res.data
                });
            },
            //***********查询登记老人列表*****************
            queryRegistPersonList() {
                axios.get(''+global.HOST+'/IntegratedQueryPage/queryRegistPersonList?' +
                    'pageNum=' + this.pageNum + '&pageSize=' + this.pageSize + '&personName=' + this.person.person_name + '' +
                    '&bedNumber=' + this.person.person_bednumber
                    + '&sex=' + this.person.radio2 + '&currentDevice=' + this.person.value2 + '&jgbm=' + this.person.value
                ).then(res => {
                    this.person.total = res.data.total;
                    this.person.list = res.data.rows
                });
            },
            queryRegistListDetail() {  //点击查询
                this.pageNum = 1;
                this.queryRegistPersonList();
            },
            checkInfoByPagination(e) { // 分页查询老人信息
                this.pageNum = e;
                this.queryRegistPersonList();
            },
            lookDetailInfo(yngrbsh, jgbm) { //查看详情
                console.log(jgbm);
            },
            //***********查询设备列表*****************
            queryDeviceList() {
                var startDate = '';
                var endDate = '';
                if (this.formInline.dataValue == null) {
                    startDate = "";
                    endDate = "";
                } else {
                    startDate = this.formInline.dataValue[0];
                    endDate = this.formInline.dataValue[1];
                    if (startDate == undefined) {
                        startDate = '';
                    } else {
                        var time = new Date(startDate)
                            , year = time.getFullYear()
                            , month = (time.getMonth() + 1) < 10 ? '0' + (time.getMonth() + 1) : (time.getMonth() + 1)
                            , date = time.getDate() < 10 ? '0' + time.getDate() : time.getDate();
                        startDate = year + "-" + month + "-" + date;
                    }
                    if (endDate == undefined) {
                        endDate = '';
                    } else {
                        var time = new Date(endDate)
                            , year = time.getFullYear()
                            , month = (time.getMonth() + 1) < 10 ? '0' + (time.getMonth() + 1) : (time.getMonth() + 1)
                            , date = time.getDate() < 10 ? '0' + time.getDate() : time.getDate();
                        endDate = year + "-" + month + "-" + date;
                    }
                }

                axios.get(''+global.HOST+'/IntegratedQueryPage/queryDeviceList?' +
                    'pageNum=' + this.device.devicePageNum + '&pageSize=' + this.device.devicePageSize + '&personName=' + this.device.personName + '' +
                    '&imei=' + this.device.imei + '&deviceName=' + this.device.deviceName + '&onlineStates=' + this.device.onlineStates + '' +
                    '&jgbm=' + this.device.jgbm + '&startDate=' + startDate + '&endDate=' + endDate
                ).then(res => {
                    this.device.deviceTotal = res.data.total;
                    this.device.deviceList = res.data.rows
                });
            },
            queryDeviceListDetail() {  //点击查询设备
                this.device.devicePageNum = 1;
                this.queryDeviceList();
            },
            checkInfoByPaginationDevice(e) { // 分页查询设备信息
                this.device.devicePageNum = e;
                this.queryDeviceList();
            },

        },
    }

</script>

