    .query_detail {
        width: 60%;
        background: #ECF6FF;
        color: #459CFB;
        padding-bottom: 8px;
        padding-top: 8px;
        margin-top: 12px;
        text-align: center;
    }

    .detail_info {
        margin-top: -10px;
        margin-bottom: 10px;
        background: white;
        height: 45px;
        line-height: 42px;
        margin-top: auto;
    }

    .person_detail {
        height: 56px;
        background: white;
        /*margin-bottom: 10px;*/
        line-height: 26px;
