        background-size: cover;
    }

    .top-images-bg3 {
        background: url("../../assets/imgs/img-bjfl.png");
        background-size: cover;
    }

    .top-images-bg4 {
        background: url("../../assets/imgs/img-bjcl.png");
        background-size: cover;
    }

    .top-images-bg5 {
        background: url("../../assets/imgs/img-gjzz.png");
        background-size: cover;
    }

    .top-images .left-icon {
        width: 50%;
        height: 100%;
        /*text-align: center;*/
        /*margin-top: 36px;*/
        position: relative;
