<template>
    <div id="switchWrap">
        <div
                :class="[activated===1? 'active':'']"
                @click="switchChoose(1)"
        >
            <div>
                <img
                        src="../../assets/imgs/icon-gjjk.png"
                        height="25"
                        width="25"
                >
            </div>
            <span>轨迹监控</span>
        </div>

        <span class="dividing"/>

        <div
                :class="[activated===2? 'active':'']"
                @click="switchChoose(2)"
        >
            <div>
                <img
                        src="../../assets/imgs/icon-dlwl.png"
                        height="25"
                        width="25"
                >
            </div>
            <span>地理围栏</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'SwitchMapType',
        data() {
            return {
                activated: 0
            }
        },
        mounted() {
            console.log(this);
        },
        methods: {
            switchChoose(index) {
                (index === this.activated) && (index = 11)
                this.activated = index
                this.$emit('locationMapType', index)
            }
        }
    }
</script>

<style scoped>
    #switchWrap {
        left: 15px;
        top: 100px;
        z-index: 7;
        width: 70px;
        padding: 7px;
        height: 140px;
        color: #333333;
        font-size: 14px;
        background: white;
        border-radius: 8px;
        position: absolute;
        box-shadow: 0 3px 5px 2px lightgray;

    }

    #switchWrap > div {
        display: inline-block;
        height: 60px;
        width: 68px;
        cursor: pointer;
        border: 1px solid rgba(0, 0, 0, 0);
        border-radius: 5px;
    }

    #switchWrap > div:hover, .active {
        border: 1px solid rgb(81, 174, 247) !important;
        background: rgb(238, 247, 254);
    }

    #switchWrap img {
        margin-top: 5px;
    }

    .dividing {
        display: inline-block;
        background: #eeeeee;
        margin-bottom: 4px;
        width: 55px;
        height: 1px;
    }

</style>



