<template>
    <div id="app">
        <v-header />
        <v-bmap :state="state" />
    </div>
</template>

<script>

    import header from '../../components/common/title';
    import bMap from '../../components/map/bMap.vue'

    export default {
        components: {
            'v-header': header,
            'v-bmap': bMap
        },
        data() {
            return {
                state: {
                    userCount: true,
                    mapState: 'gis'
                }
            }
        },
        computed: {
            username() {
                // We will see what `params` is shortly
                return this.$route.params.username
            }
        },
        mounted() {
        },
        methods: {
            startHacking() {
                this.$notify({
                    title: 'It works!',
                    type: 'success',
                    message: 'We\'ve laid the ground work for you. It\'s time for you to build something epic!',
                    duration: 5000
                })
            },
            goBack() {
                window.history.length > 1
                    ? this.$router.go(-1)
                    : this.$router.push('/')
            }
        },
    }
</script>

<style>

    #app {
        font-family: Helvetica, sans-serif;
        text-align: center;
    }
</style>



