        top: 60px;
        bottom: 0;
        width: 100%;
        background-color: #f2f4f5;
    }

    .view-con {
        /*background-color: #ffffff;*/
        /*min-height: calc(100% - 30px);*/
        width: 100%;
        margin: 15px 0;
    }

    .view-title {
        height: 60px;
        padding: 12px 0;
        width: 100%;
        line-height: 36px;
        background-color: #ffffff;
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
        margin-left: 0 !important;
        margin-right: 0 !important;
    }

    .equip-det {
        border-right: 1px solid #dddddd;
        height: 30px;
        margin-top: 3px;
        line-height: 30px;
    }

    .back {
        width: 24px;
        height: 24px;
        background-color: #c7cbd1;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
        margin-top: 3px;
        margin-right: 10px;
        margin-left: 20px;
        color: #ffffff;
        line-height: 24px;
        text-align: center;
        font-size: 16px;
        font-weight: bolder;
        cursor: pointer;
    }

    .view-name {
        display: inline-block;
        color: #333333;
        font-size: 16px;
    }

    .equip-img {
        vertical-align: middle;
        width: 36px;
        margin-left: 8px;
        margin-right: 8px
    }

    .info-name {
        color: #999999;
        margin-right: 4px;
    }

    .info-det {
        color: #333333;
    }

    .view-table {
        background-color: #ffffff;
        min-height: calc(100% - 70px);
        width: calc(100% - 30px);
        padding: 15px;
        margin: 15px;
    }

    .table-line {
        border-bottom: 1px solid #ffffff;
    }

    .table-title {
        height: 39px;
        line-height: 39px;
        background-color: #f0f3f5;
        padding-left: 16px;
        color: #333333;
        font-size: 14px;
    }

    .table-con {
