        height: 39px;
        line-height: 39px;
        padding-left: 16px;
        color: #666666;
        font-size: 14px;
    }
</style>

<script>
    import axios from 'axios'
    import ElRow from "element-ui/packages/row/src/row";
    import ElCol from "element-ui/packages/col/src/col";

    export default {
        name: "ViewEquipDetails",
        components: {
            ElCol,
            ElRow
//            ElButton,
//            ElForm,
//            ElFormItem,
//            ElCol,
//            ElRow
//            // 'v-header': header
        },
        data() {
            return {
                equip: {},
                back: '' //enum
            }
        },
        mounted() {
            // console.log(this.$parent);
            console.log(this.$parent.equipPageLastPage);
            console.log(this.$parent.currentEquipment);
            this.back = this.$parent.equipPageLastPage === 'user' ? 'userDes' : 'equipDes';
            axios(`${global.HOST}/device/${this.$parent.currentEquipment.imei}`)
                .then(res => {
                    console.log(res);
                    this.equip = res.data;
                })
        },
        methods: {
            pageStateSwitch(e) {
                console.log(e);
                this.$emit('changeStatus', e)
            },
        },
    }
</script>



