<template>
    <div>
        <v-header/>
        <div
                v-if="pageState==='list'"
                class="content"
        >
            <div class="totalState">
                <div
                        class="totalStateItem"
                        :class="totalState==='user'&& 'active'"
                        @click="totalStateSwitch('user')"
                >
                    <span>
                        <img v-if="totalState==='user'" src="../../assets/imgs/icon-yhgl-hover.png">
                        <img v-else src="../../assets/imgs/icon-yhgl.png">
</span>
                    <span>用户管理</span>
                </div>
                <div
                        class="totalStateItem"
                        :class="totalState==='equipment'&& 'active'"
                        @click="totalStateSwitch('equipment')"
                >
                    <span>
                <img v-if="totalState==='equipment'" src="../../assets/imgs/icon-sbgl-hover.png">
                <img v-else src="../../assets/imgs/icon-sbgl.png">
</span>
                    <span>设备管理</span>
                </div>

                <div id="burger">
                    <img
                            src="../../assets/imgs/button-table.png"
                            alt=""
                    >
                </div>
            </div>

            <div
                    v-if="totalState==='user'"
                    class="conditionWrap"
            >
                <div class="statusWrap">
                    <div class="search1">
                        <span>用户名称</span>
                        <el-input
                                v-model="user.search"
                                size="small"
                                placeholder="请输入内容"
                                suffix-icon="el-icon-search"
                        />
                    </div>

                    <div class="radioWrap">
                        <span>佩戴设备</span>
                        <el-radio-group v-model="user.own">
                            <el-radio :label="-1">
                                全部
                            </el-radio>
                            <el-radio :label="0">
                                有
                            </el-radio>
                            <el-radio :label="1">
                                无
                            </el-radio>
                        </el-radio-group>
                    </div>

                    <div class="radioWrap">
                        <span>报警</span>
                        <el-radio-group v-model="user.alarm">
                            <el-radio :label="-1">
                                全部
                            </el-radio>
                            <el-radio :label="0">
                                有
                            </el-radio>
                            <el-radio :label="1">
                                无
                            </el-radio>
                        </el-radio-group>
                    </div>

                    <div class="btn-container">
                        <el-button
                                type="primary"
                                size="small"
                                :class="pageState==='newUser'"
                                @click="pageStateSwitch('newUser',{})"
                        >
                            新增用户
                        </el-button>
                    </div>
                </div>

                <el-row :gutter="15">
                    <!--  TODO 根据不同设备类型显示不同图标 -->
                    <el-col
                            v-for="(item,index) in userData"
                            :key="index"
                            :span="6"
                    >
                        <div class="level-bg">

                        </div>
                        <span class="level-text">
                            {{item.nursinglevelname}}
                        </span>
                        <div
                                class="itemWrap"
                                :class="pageState==='userDes'"
                                @click="pageStateSwitch('userDes',item)"
                        >

                            <div class="cardInfo">
                                <span
                                        class="name"
                                        v-text="item.name"
                                />
                                <span
                                        class="info"
                                        v-text="item.gender"
                                />
                                <span
                                        class="info"
                                        v-text="item.age"
                                />岁
                            </div>
                            <div
                                    v-if="item.devices.length>0"
                                    class="equipList"
                            >
                                <el-row
                                        v-for="(o,i) in item.devices.slice(0,3)"
                                        :key="i"
                                        :gutter="15"
                                >
                                    <el-col :span="12">
                                        <img
                                                src="../../assets/imgs/img-shoubiao.png"
                                                alt=""
                                        >
                                        <span class="equip">
{{ o.deviceName }}
</span>
                                    </el-col>
                                    <el-col :span="4">
                                        <span :class="o.oneline==='在线'?'status':'lixian status'">
{{ o.oneline }}
</span>
                                    </el-col>
                                    <el-col :span="4">
                                        <span class="sos">
SOS
</span>
                                    </el-col>
                                    <el-col v-if="o.GPS==='1'" :span="2">
                                        <span class="sos">
                                            <img
                                                    src="../../assets/imgs/icon-local.png"
                                                    style="width: 15px;height: 19px"
                                                    @click.stop="changeStatus('trackManage',item)"
                                            >
</span>
                                    </el-col>
                                </el-row>
                            </div>

                            <div
                                    v-else
                                    class="noData"
                            >
                                <img
                                        src="../../assets/imgs/img-noData.png"
                                        alt=""
                                >
                                <p>该老人暂无绑定设备</p>
                            </div>
                            <p
                                    v-if="item.devices.length>3"
                                    class="omit"
                            >
                                ···
                            </p>
                        </div>
                    </el-col>
                </el-row>

                <el-pagination
                        v-if="totalUserCount > userPageSize"
                        background
                        align="right"
                        :page-size="userPageSize"
                        layout="prev, pager, next"
                        :total="totalUserCount"
                        @current-change="queryUser"
                />
            </div>

            <div
                    v-if="totalState==='equipment'"
                    class="conditionWrap"
            >
                <div class="statusWrap">
                    <div class="search1">
                        <span>设备名称</span>
                        <el-input
                                v-model="equipment.search"
                                size="small"
                                placeholder="请输入内容"
                                suffix-icon="el-icon-search"
                        />
                    </div>
                    <div class="radioWrap">
                        <span>是否绑定</span>
                        <el-radio-group v-model="equipment.bind">
                            <el-radio :label="-1">
                                全部
                            </el-radio>
                            <el-radio :label="0">
                                已绑定
                            </el-radio>
                            <el-radio :label="1">
                                未绑定
                            </el-radio>
                        </el-radio-group>
                    </div>
                    <div class="radioWrap">
                        <span>状态</span>
                        <el-radio-group v-model="equipment.status">
                            <el-radio :label="-1">
                                全部
                            </el-radio>
                            <el-radio :label="0">
                                离线
                            </el-radio>
                            <el-radio :label="1">
                                在线
                            </el-radio>
                        </el-radio-group>
                    </div>
                    <div class="btn-container">
                        <el-button
                                type="primary"
                                size="small"
                                :class="pageState==='addEquipment'"
                                @click="pageStateSwitch('addEquipment',{})"
                        >
                            新增设备
                        </el-button>
                    </div>
                </div>

                <el-row :gutter="15">

                    <!--:key="item.imei"-->
                    <el-col
                            v-for="(item,index) in equipmentData"
                            :key="index"
                            :span="25"
                    >
                        <div
                                class="itemWrap1"
                                :class="pageState==='equipment'"
                                @click="pageStateSwitch('equipDes',item.imei)"
                        >
                            <div class="equipDes">
                                <img
                                        src="../../assets/imgs/img-shoubiao.png"
                                        alt=""
                                >
                                <div class="desWrap">
                                    <h6 v-text="item.sbmc"/>
                                    <p>绑定用户:<span v-text="Number(item.bdzt)===1?item.xm:'未绑定'"/></p>
                                </div>
                            </div>
                            <!-- 离线type是info -->
                            <el-button
                                    v-if="item.zxzt==='在线'"
                                    type="success"
                                    round
                            >
                                在线
                            </el-button>
                            <el-button
                                    v-if="item.zxzt==='离线'"
                                    type="info"
                                    round
                            >
                                离线
                            </el-button>
                        </div>
                    </el-col>
                </el-row>

                <el-pagination
                        v-if="totalEquipmentCount>equipmentPageSize"
                        background
                        align="right"
                        :page-size="equipmentPageSize"
                        layout="prev, pager, next"
                        :total="totalEquipmentCount"
                        @current-change="queryEquipment"
                />
            </div>
        </div>

        <v-user-detail
                v-if="pageState==='userDes'"
                :current-user="currentUser"
                @changeCurrentEquipment="changeCurrentEquipment"
                @changeStatus="changeStatus"
                @showDetail="showDetail('user')"

        />

        <!-- 设备详情 -->
        <v-equipment-detail
                v-if="pageState==='equipDes'"
                :device-id="imei"
                @changeStatus="changeStatus"
                @showDetail="showDetail('equip')"
        />
        <!-- 新建用户页面 -->
        <v-new-user
                v-if="pageState==='newUser'"
                @changeStatus="changeStatus"
        />
        <!-- 新增设备 -->
        <v-new-equip
                v-if="pageState==='addEquipment'"
                @changeStatus="changeStatus"
        />

        <v-view-equip-details
                v-if="pageState==='viewEquipDetails'"
                @changeStatus="changeStatus"
        />
        <!--报警信息页面-->
        <v-alarm
                v-if="pageState==='alarmManage'"
                @changeStatus="changeStatus"
        />
        <!-- 体征信息页面 -->
        <v-sign-info
                v-if="pageState==='signInfo'"
                @changeStatus="changeStatus"
        />

        <!-- 围栏管理页面 -->
        <v-fence-manage
                v-if="pageState==='fenceManage'"
                @changeStatus="changeStatus"
        />

        <!-- 轨迹管理页面 -->
        <v-orbit-manage
                v-if="pageState==='trackManage'"
                @changeStatus="changeStatus"
        />
        <!-- 报警策略页面 -->
        <v-alarm-tactics v-if="pageState==='alarmTactics'" @changeStatus="changeStatus"/>
    </div>
</template>

<script>

    import axios from 'axios'

    import header from '../../components/common/title';
    import ElCol from "element-ui/packages/col/src/col";
    import ElRow from "element-ui/packages/row/src/row";
    import Middleware from '../common/middleware.js';


    import userTemp from '../../components/workspace/userDes';
    import equipTemp from '../../components/workspace/equipDes';
    import newTemp from '../../components/workspace/newUser';
    import viewEquipDetails from '../../components/workspace/viewEquipDetails'
    import alarm from '../../components/workspace/alarmManage'
    import signInfo from '../../components/workspace/signInfo'
    import fenceManage from '../../components/workspace/fenceManage'
    import orbitManage from '../../components/workspace/orbitManage'
    import addEquipment from '../../components/workspace/addEquipment'
    import alarmTactics from '../../components/workspace/alarmTactics'

    export default {
        name: "Workspace",
        components: {
            ElRow,
            ElCol,
            'v-alarm': alarm,
            'v-header': header,
            'v-new-user': newTemp,
            'v-sign-info': signInfo,
            'v-user-detail': userTemp,
            'v-new-equip': addEquipment,
            'v-fence-manage': fenceManage,
            'v-orbit-manage': orbitManage,
            'v-equipment-detail': equipTemp,
            'v-alarm-tactics': alarmTactics,
            'v-view-equip-details': viewEquipDetails,
        },
        data() {
            return {
                totalState: 'user', // enum:['user','equipment']
                user: {
                    search: '',
                    own: -1,
                    alarm: -1 //[-1 all; 0 has;1 none]
                },
                equipment: {
                    search: '',
                    bind: -1,
                    status: -1
                },
                pageState: 'list', // enum : [list 列表页面 ,userDes 用户详情 ,newUser 新建用户,equipDes 设备详情,viewEquipDetails 查看设备详情,alarm 报警管理,signInfo 体征信息,fenceManage 围栏管理,orbitManage 轨迹管理,alarmTactics 报警策略]
                userDes: {},
                equipDes: {},
                equipmentData: [],
                userData: [],
                totalUserCount: 0,
                totalEquipmentCount: 0,
                userPageSize: 16,
                equipmentPageSize: 30,
                imei: '',
                currentUser: '',
                currentEquipment: '',
                equipPageLastPage: '', // enum:[user,equip]
                currentPatientid: '',
                orbitMapBack: 'workspace',  // enum [user ,workspace ]
            }
        },
        watch: {
            // watching the object fromInline
            user: {
                handler() {
                    this.queryUser()
                },
                deep: true
            },
            equipment: {
                handler() {
                    this.queryEquipment()
                },
                deep: true
            },
        },
        mounted() {
            this.queryUser()
            this.queryEquipment()
        },
        methods: {
            totalStateSwitch(state) {
                this.totalState = state;
            },
            pageStateSwitch(state, obj) { // 切换用户管理与设备管理
                state.includes('equipDes') && (this.imei = obj)
                state.includes('userDes') && (this.currentUser = obj)
                // state.includes('equipment') && (this.imei = imei)
                // console.log(obj);

                this.pageState = state;
            },
            changeStatus(e, user) {


                if (e === 'trackManage' && user) {
                    this.currentUser = user;
                    this.$store.commit('updateMapTrackBack', 'workspace')
                    axios(`${global.HOST}/entity/${user.patientid}/location`)
                        .then(res => {
                            res.data.point && Middleware.$emit('displayLocation', {
                                lng: res.data.point.longitude,
                                lat: res.data.point.latitude
                            })
                            this.$parent.orbitMapBack = 'user';
                        })
                }

                // e.includes('list') && this.totalState === 'user' ? this.queryUser() : this.queryEquipment()
                this.pageState = e
            },
            queryEquipment(page = 1) {
                axios.get(`${global.HOST}/device/list?pageSize=${this.equipmentPageSize}&pageNum=${Number(page)}&binding=${this.equipment.bind}&online=${this.equipment.status}&deviceName=${this.equipment.search}`)
                    .then(res => {
                        // console.log(res);
                        this.equipmentData = res.data.rows;
                        this.totalEquipmentCount = res.data.total;
                        // console.log(res.data.rows);
                    })
            },
            queryUser(page = 1) {
                axios.get(`${global.HOST}/entity/list?pageSize=${this.userPageSize}&pageNum=${Number(page)}&binding=${this.user.own}&alarm=${this.user.alarm}&patientId=&patientName=${this.user.search}`)
                    .then(res => {
                        this.userData = res.data.rows;
                        console.log(this.userData);
                        this.totalUserCount = res.data.total;
                    })
            },
            jumpPosition() {
                open('/?type=equipStevenLee&lat=11&lng=22', '_self');
                // this.$router.push({
                //     path:'/',   //跳转的路径
                //     query:{
                //         id:this.id ,
                //     }
                // })
            },
            changeCurrentEquipment(data) {
                this.currentEquipment = data;
            },
            showDetail(type) {
                this.equipPageLastPage = type
                console.log(type);
                this.changeStatus('viewEquipDetails');

            }
        },
    }

</script>

<style scoped>
    /* tip: using font-awesome icon */
    /*@import "~font-awesome/css/font-awesome.css";*/

    * {
        font-size: 14px;
    }

    .el-col-25 {
        width: 20%;
    }

    ul, li {
        list-style: none;
    }

    .content {
        padding: 15px;
        background-color: #f2f4f5;
    }

    p, h1, h2, h3, h4, h5, h6 {
        margin: 0;
        padding: 0;
    }

    #burger {
        float: right;
        cursor: pointer;
        display: inline-block;
        vertical-align: middle;
    }

    #burger > image {
        /*vertical-align: -webkit-baseline-middle;*/
    }

    .totalState {
        width: 100%;
        height: 30px;
        border-bottom: 1px solid rgb(64, 176, 254);
    }

    .totalStateItem {
        color: #666666;
        height: 29px;
        width: 120px;
        border: 1px solid rgb(64, 176, 254);
        display: inline-block;
        line-height: 30px;
        text-align: center;
        margin-right: 15px;
        border-radius: 5px 5px 0 0;
        cursor: pointer;
        background-color: #ffffff;
    }

    .totalState > .active {
        color: white;
        background: linear-gradient(90deg, rgb(53, 114, 255), rgb(60, 171, 242));
    }

    .totalStateItem img {
        vertical-align: middle;
    }

    .conditionWrap {
        width: 100%;
        margin-top: 10px;
    }

    .conditionWrap .statusWrap {
        width: 100%;
        min-width: 1080px;
        min-height: 60px;
        line-height: 60px;
        background-color: #ffffff
    }

    .conditionWrap > div > span {
        margin-right: 10px;
        color: #666666;
    }

    .radioWrap,
    .search1 {
        float: left;
        margin-left: 15px;
        margin-right: 45px;
    }

    .radioWrap span,
    .search1 span {
        margin-right: 10px;
    }

    .search1 > .el-input {
        width: 200px;
    }

    .btn-container {
        float: right;
        margin-right: 15px;
    }

    .itemWrap {
        height: 160px;
        min-width: 300px;
        border-radius: 8px;
        margin-top: 15px;
        background-color: #ffffff;
        box-shadow: 0 0 20px #e8eaed;
        cursor: pointer;
    }

    .itemWrap1 {
        height: 100px;
        border-radius: 8px;
        margin-top: 15px;
        background-color: #ffffff;
        box-shadow: 0 0 20px #e8eaed;
        cursor: pointer;
    }

    .cardInfo {
        height: 39px;
        background-color: #f9f9f9;
        border-bottom: 1px solid #eeeeee;
        border-radius: 8px 8px 0 0;
        padding-left: 12px;
        margin-left: 30px;
    }

    .cardInfo span.name {
        font-size: 16px;
        line-height: 40px;
        color: #333333;
    }

    .cardInfo span.info {
        color: #666666;
        font-size: 14px;
        line-height: 14px;
        margin-left: 15px;
    }

    .noData {
        margin: 20px auto;
        text-align: center;
    }

    .noData p {
        margin-top: 5px;
        color: #999999;
    }

    .equipList {
        margin-top: 8px;
    }

    .equipList .el-row {
        height: 30px;
    }

    .equipList img {
        width: 24px;
        height: 24px;
        vertical-align: middle;
        padding-left: 12px;
    }

    .equipList .equip {
        color: #333333;
        font-size: 14px;
        line-height: 30px;
    }

    .equipList span.sos {
        color: #ff2c2c;
        line-height: 30px;
    }

    .equipList span.status {
        color: #12bf0d;
        line-height: 30px;
    }

    .equipList span.lixian {
        color: #999999;
    }

    p.omit {
        margin: 0;
        padding: 0 12px;
        font-size: 36px;
        color: #999999;
        line-height: 16px
    }

    .equipDes {
        height: 42px;
        width: 100%;
        padding: 10px 0;
    }

    .equipDes img {
        width: 48px;
        height: 48px;
        float: left;
        padding: 0 10px;
    }

    .desWrap {
        width: calc(100% - 68px);
        height: 42px;
        float: left;
    }

    .desWrap p {
        width: 100%;
        margin: 0;
        padding: 0;
        color: #999999;
    }

    .desWrap p span {
        color: #666666;
    }

    .desWrap h6 {
        margin: 0;
        padding: 0;
        color: #333333;
        font-weight: 400;
        font-size: 16px;
        margin-bottom: 4px;
    }

    .itemWrap1 .el-button {
        float: right;
        width: 48px;
        height: 24px;
        margin-right: 10px;
        line-height: 4px;
        padding: 0;
        border-radius: 21px;
    }

    .el-button--info {
        background-color: #b8b8b8;
        border-color: #b8b8b8;
    }

    .level-bg {
        width: 0;
        height: 0;
        float: left;
        font-size: 12px;
        margin-top: 15px;
        color: firebrick;
        display: inline-block;
        border-radius: 8px 0 0;
        border: 20px solid transparent;
        border-top-color: rgb(80, 211, 247);
        border-left-color: rgb(80, 211, 247);
        background: linear-gradient(rgb(134, 170, 252), rgb(158, 215, 253));
    }

    .level-text {
        height: 0;
        float: left;
        color: white;
        font-size: 12px;
        margin-top: 22px;
        margin-left: -46px;
        transform: rotate(-45deg);
    }
</style>



