<template>
    <div>
        <div class="view">
            <div class="view-con">
                <el-row
                        :gutter="10"
                        class="view-title"
                >
                    <el-col :span="3">
                        <div class="equip-det">
                            <i
                                    class="el-icon-arrow-left back"
                                    @click="pageStateSwitch('userDes')"
                            />
                            <h5 class="view-name">
                                围栏管理
                            </h5>
                        </div>
                    </el-col>
                    <el-col :span="5">
                        <img
                                src="../../assets/imgs/img-shoubiao.png"
                                alt=""
                                class="equip-img"
                        >
                        <span class="info-name">
设备名称:
</span>
                        <span class="info-det">
爱牵挂s3pro
</span>
                    </el-col>
                    <el-col :span="3">
                        <span class="info-name">
当前用户:
</span>
                        <span class="info-det">
徐美林
</span>
                    </el-col>
                    <el-col :span="5">
                        <span class="info-name">
床位/地址:
</span>
                        <span class="info-det">
厦门市湖底区名社区203室
</span>
                    </el-col>
                    <el-col :span="2">
                        <span class="info-name">
当前电量
</span>
                        <span class="info-det">
50%
</span>
                    </el-col>
                    <el-col :span="2">
                        <span class="info-name">
当前状态:
</span>
                        <span class="info-det">
在线
</span>
                    </el-col>
                </el-row>
                <el-row class="view-table">
                    <v-bmap :state="state"/>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
    import bMap from '../../components/map/bMap.vue'

    export default {
        name: "FenceManage",
        components: {
            'v-bmap': bMap
        },
        data() {
            return {
                state: {
                    userCount: false,
                    mapState: 'fence'
                }
            }
        },
        methods: {
            pageStateSwitch(e) {
                this.$emit('changeStatus', e)
            },
        },
    }
</script>


<style scoped>

    .view {
        position: absolute;
        top: 60px;
        bottom: 0;
        width: 100%;
        background-color: #f2f4f5;
    }

    .view-con {
        /*background-color: #ffffff;*/
        /*min-height: calc(100% - 30px);*/
        width: 100%;
        margin: 15px 0;
    }

    .view-title {
        height: 60px;
        padding: 12px 0;
        width: 100%;
        line-height: 36px;
        background-color: #ffffff;
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
        margin-left: 0 !important;
        margin-right: 0 !important;
    }

    .equip-det {
        border-right: 1px solid #dddddd;
        height: 30px;
        margin-top: 3px;
        line-height: 30px;
    }

    .back {
        width: 24px;
        height: 24px;
        background-color: #c7cbd1;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
        margin-top: 3px;
        margin-right: 10px;
        margin-left: 20px;
        color: #ffffff;
        line-height: 24px;
        text-align: center;
        font-size: 16px;
        font-weight: bolder;
        cursor: pointer;
    }

    .view-name {
        display: inline-block;
        color: #333333;
        font-size: 16px;
    }

    .equip-img {
        vertical-align: middle;
        width: 36px;
        margin-left: 8px;
        margin-right: 8px
    }

    .info-name {
        color: #999999;
        margin-right: 4px;
    }

    .info-det {
        color: #333333;
    }

    .view-table {
        background-color: #ffffff;
        min-height: calc(100% - 70px);
        width: calc(100% - 30px);
        padding: 15px;
        margin: 15px;
    }
</style>



