import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        mapTrackBack: 'workspace', //enum [workspace,user]
        count: 101
    },
    mutations: {
        updateMapTrackBack: (state, type) => state.mapTrackBack = type,
    },
    getters:{
        count: function (state) {
            return state.count += 22
        }
    }
})



export default store




