<template>
    <div>
        <div class="signInfo">
            <div class="title">
                <el-row :gutter="30" class="title-row">
                    <el-col :span="3">
                        <div class="tit">
                            <i class="el-icon-arrow-left back" @click="pageStateSwitch('userDes')"/>
                            <h5>体征信息</h5>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <el-form>
                            <el-form-item label="设备名称:">
                                <el-select v-model="sign.signName" placeholder="爱牵挂" size="small">
                                    <el-option label="爱牵挂1" value="value1"></el-option>
                                    <el-option label="爱牵挂2" value="value2"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :span="3">
                        <span class="info-name">当前用户:</span>
                        <span class="info-det">徐美林</span>
                    </el-col>
                    <el-col :span="5">
                        <span class="info-name">床位/地址:</span>
                        <span class="info-det">厦门市湖底区名社区203室</span>
                    </el-col>
                    <el-col :span="3">
                        <span class="info-name">当前电量</span>
                        <span class="info-det">50%</span>
                    </el-col>
                    <el-col :span="3">
                        <span class="info-name">当前状态:</span>
                        <span class="info-det">在线</span>
                    </el-col>
                </el-row>
            </div>
            <div class="content">
                <div class="con-left left">
                    <ul>
                        <li class="leftList" :class="totalState==='blood_pressure'&& 'active'"
                            @click="totalStateSwitch('blood_pressure')">
                            <img src="../../assets/imgs/img-xy.png" alt="">
                            <h4>血压</h4>
                        </li>
                        <li class="leftList" :class="totalState==='pulse_rate'&& 'active'"
                            @click="totalStateSwitch('pulse_rate')">
                            <img src="../../assets/imgs/img-xinlv.png" alt="">
                            <h4>心率</h4>
                        </li>
                        <li class="leftList" :class="totalState==='step_count'&& 'active'" @click="totalStateSwitch('step_count')">
                            <img src="../../assets/imgs/img-jibu.png" alt="">
                            <h4>计步</h4>
                        </li>
                        <li class="leftList" :class="totalState==='sleep_data'&& 'active'"
                            @click="totalStateSwitch('sleep_data')">
                            <img src="../../assets/imgs/img-shuimin.png" alt="">
                            <h4>睡眠</h4>
                        </li>
                    </ul>
                </div>
                <div class="con-right left">
                    <div v-if="totalState==='blood_pressure'">
                        <div class="rightWrap">
                            <el-tabs v-model="blood" type="card" @tab-click="handleClick">
                                <el-tab-pane label="今日数据" name="day">
                                </el-tab-pane>
                                <el-tab-pane label="最近七天" name="week"></el-tab-pane>
                                <el-tab-pane label="最近一月" name="moth" ></el-tab-pane>
                                <el-tab-pane label="最近半年" name="half" ></el-tab-pane>
                                <div class="chart">
                                    <IEcharts
                                            :option="bloodCharts.option"
                                    />
                                </div>
                                <div class="data">
                                    <h3>详细数据</h3>
                                    <el-table
                                            :data="todayData"
                                            stripe
                                            style="width: 100%">
                                        <el-table-column
                                                type="index"
                                                label="序号"
                                                align="center">
                                        </el-table-column>
                                        <el-table-column
                                                prop="ssy"
                                                label="收缩压"
                                                align="center">
                                        </el-table-column>
                                        <el-table-column
                                                prop="szy"
                                                label="舒张压"
                                                align="center">
                                        </el-table-column>
                                        <el-table-column
                                                prop="ssy_high"
                                                label="参考值上限"
                                                align="center">
                                        </el-table-column>
                                        <el-table-column
                                                prop="szy_low"
                                                label="参考值下限"
                                                align="center">
                                        </el-table-column>
                                        <el-table-column
                                                prop="sbsj"
                                                label="采集时间"
                                                align="center">
                                        </el-table-column>
                                    </el-table>
                                    <el-pagination
                                            v-if="totalUserCount > userPageSize"
                                            background
                                            align="right"
                                            :page-size="userPageSize"
                                            layout="prev, pager, next"
                                            :total="totalUserCount"
                                            @current-change="querySignInfoList"
                                    />
                                </div>
                            </el-tabs>
                        </div>

                    </div>
                    <div v-if="totalState==='heart'">心率</div>
                    <div v-if="totalState==='pulse'">脉搏</div>
                    <div v-if="totalState==='step'">
                        <div class="rightWrap">
                            <div class="banner">
                                <img src="../../assets/imgs/img-banner-jibu.png" alt="" style="width: 100%;">
                                <div class="bannerWrap">
                                    <div class="deep left color255 borderRight">
                                        <p class="hour"><span>23232</span></p>
                                        <p>昨日步数</p>
                                    </div>
                                    <div class="today left color255">
                                        <p class="hour48"><span>31232</span></p>
                                        <p>今日步数</p>
                                    </div>
                                    <div class="deep right color255 borderLeft">
                                        <p class="hour"><span>12121</span></p>
                                        <p>近30日日均步数</p>
                                    </div>
                                </div>
                            </div>
                            <div class="data">
                                <h3>详细数据</h3>
                                <el-table
                                        :data="tableData"
                                        stripe
                                        style="width: 100%">
                                    <el-table-column
                                            prop="date"
                                            label="日期"
                                            align="center">
                                    </el-table-column>
                                    <el-table-column
                                            prop="today"
                                            label="当日睡眠(h)"
                                            align="center">
                                    </el-table-column>
                                    <el-table-column
                                            prop="deep"
                                            label="深睡时长(h)"
                                            align="center">
                                    </el-table-column>
                                    <el-table-column
                                            prop="simple"
                                            label="浅睡时长(h)"
                                            align="center">
                                    </el-table-column>
                                </el-table>
                            </div>
                        </div>
                    </div>
                    <div v-if="totalState==='sleep_data'">
                        <div class="rightWrap">
                            <div class="banner">
                                <img src="../../assets/imgs/img-sleep.png" alt="" style="width: 100%;">
                                <div class="bannerWrap">
                                    <div class="deep left color255">
                                        <p class="hour"><span>0</span>小时</p>
                                        <p>深睡</p>
                                    </div>
                                    <div class="today left color255">
                                        <p class="hour48"><span>0</span>小时</p>
                                        <p>今日睡眠</p>
                                    </div>
                                    <div class="deep right color255">
                                        <p class="hour"><span>0</span>小时</p>
                                        <p>浅睡</p>
                                    </div>
                                </div>
                            </div>
                            <div class="data">
                                <h3>详细数据</h3>
                                <el-table
                                        :data="tableData"
                                        stripe
                                        style="width: 100%">
                                    <el-table-column
                                            prop="date"
                                            label="日期"
                                            align="center">
                                    </el-table-column>
                                    <el-table-column
                                            prop="sleepTime"
                                            label="当日睡眠(h)"
                                            align="center">
                                    </el-table-column>
                                    <el-table-column
                                            prop="deepSleep"
                                            label="深睡时长(h)"
                                            align="center">
                                    </el-table-column>
                                    <el-table-column
                                            prop="lightSleep"
                                            label="浅睡时长(h)"
                                            align="center">
                                    </el-table-column>
                                </el-table>
                            </div>
                        </div>
                    </div>

                </div>
                <!--<div class="con-right left"></div>-->
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import IEcharts from 'vue-echarts-v3/src/full.js';
    import ElRow from "element-ui/packages/row/src/row";
    import ElCol from "element-ui/packages/col/src/col";
    import ElForm from "../../../node_modules/element-ui/packages/form/src/form.vue";
    import ElFormItem from "../../../node_modules/element-ui/packages/form/src/form-item.vue";

    export default {
        name: "SignInfo",
        components: {
            ElFormItem,
            ElForm,
            ElCol,
            ElRow,
            IEcharts
//            ElButton,
//            ElForm,
//            ElFormItem,
//            ElCol,
//            ElRow
//            // 'v-header': header
        },
        data() {
            return {
                totalUserCount: 0,
                totalEquipmentCount: 0,
                userPageSize: 10,
                totalState: 'blood_pressure', // enum:['user','equipment']
                sign: {
                    signName: '',
                },
                blood: 'day',
                tableData: [],
                todayData: [],
                bloodCharts: {
                    option: {
                        tooltip: {
                            trigger: 'axis'
                        },
                        legend: {
                            data: ['收缩压', '舒张压']
                        },
                        xAxis: {
                            type: 'category',
                            data: [["2000-06-05",116],["2000-06-06",129],["2000-06-07",135],["2000-06-08",86],["2000-06-09",73],["2000-06-10",85],["2000-06-11",73],]
        },
                        yAxis: {
                            // type: 'value'
                        },
                        series: [{
                            data: [820, 932, 901, 934, 1290, 1330, 1320],
                            name: '收缩压',
                            type: 'line'
                        }, {
                            data: [220, 532, 201, 634, 1290, 130, 870],
                            name: '舒张压',
                            type: 'line'
                        }
                        ]
                    },

                }

            }
        },
        mounted() {
            this.querySignInfoList();
        },
        methods: {
            pageStateSwitch(e) {
                console.log(e);
                this.$emit('changeStatus', e)
            },
            totalStateSwitch(state) {
                console.log(state);
                var range = ""
                if(state=="sleep_data"){
                    range =  "half";
                }else{
                    range = this.blood;
                }
                this.totalState = state;
                axios(`${global.HOST}/vital_sign/list/${this.totalState}/${this.$parent.currentUser.patientid}`,
                    {
                        params: {
                            pageNum: 1,
                            pageSize: this.userPageSize,
                            range: range
                        }
                    }).then((res) => {
                    console.log(res)
                    if(state=="sleep_data"){
                        let obj ={};
                        this.tableData = res.data.rows;
                        res.data.rows.forEach(function(item,i){
                            //0,0时无效数据，0，255是醒着的时间，过滤，计算睡眠时间
                            let sleepTime = item.sj.split('|').filter(v =>v!=='0,0' && v!='0,255').length/2;
                            //浅睡眠
                            let lightSleep =  item.sj.split('|').filter(v =>v!=='0,0' && v!='0,255').filter(v=>v.includes('0,')).length/2;
                            //深度睡眠
                            let deepSleep = item.sj.split('|').filter(v =>v!=='0,0' && v!='0,255').filter(v=>v.includes('2,')).length/2;

                            obj.date = item.sbsj;
                            obj.sleepTime = sleepTime;
                            obj.lightSleep = lightSleep;
                            obj.deepSleep = deepSleep;
                            this.tableData.push(obj);
                        });
                    }

                });
            },
            handleClick(tab, event,page =1) {
                console.log(tab, event);
                console.log(this.$parent.currentUser)
                axios(`${global.HOST}/vital_sign/list/${this.totalState}/${this.$parent.currentUser.patientid}`,
                    {
                        params: {
                            pageNum:Number(page),
                            pageSize: 10,
                            range: this.blood
                        }
                    }).then((res) => {
                    console.log(res.data.rows);
                    this.totalUserCount = res.data.total;
                    this.todayData = res.data.rows;
                });
            },
            querySignInfoList(page=1) {
                console.log(this.$parent.currentUser)
                axios(`${global.HOST}/vital_sign/list/${this.totalState}/${this.$parent.currentUser.patientid}`,
                    {
                        params: {
                            pageNum:Number(page),
                            pageSize: 10,
                            range: this.blood
                        }
                    }).then((res) => {
                    console.log(res.data.rows);
                    this.totalUserCount = res.data.total;
                    this.todayData = res.data.rows;
                });
            }

        },
    }

</script>

<style scoped>
    @import '../../assets/css/headerTitle.css';
    @import '../../assets/css/signInfo.css';

    .chart {
        padding-top: 10px;
        width: 100%;
        height: 300px;
        border: 1px solid lightgray;
    }


</style>



