<template>
    <div class="content1">
    <div class="userWrap">
        <i class="el-icon-arrow-left back"
           @click="pageStateSwitch('list')"/>
        <img src="../../assets/imgs/img-shoubiao.png" alt="">
        <div style="display: block;float:right;margin-top: 15px;margin-left: 15px">
            <el-button type="primary" @click="submitForm(form)">保存</el-button>
        </div>
    </div>
    <div style="width:100%;text-align:center;margin-top: 60px" class="mensInfo">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <div style="margin-left: -330px">
                <font color="red">*</font>
                <el-form-item label="机构">
                    <el-select v-model="form.jgbm" placeholder="请选择机构">
                        <el-option :label="item.jgmc" :value="item.jgbm"
                                   v-for='(item,index) in form.stations' :key='index'></el-option>
                    </el-select>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="设备名称">
                    <el-input v-model="form.sbmc" placeholder="设备名称" style="width: 220px"></el-input>
                </el-form-item>
                <el-form-item label="设备代码" style="margin-left: 50px">
                    <el-input v-model="form.sbdm" placeholder="设备代码" style="width: 220px"></el-input>
                </el-form-item>
            </div>
            <div>
                <font color="red">*</font>
                <el-form-item label="IMEI" style="margin-left: 30px">
                    <el-input v-model="form.imei" placeholder="IMEI" style="width: 220px"></el-input>
                </el-form-item>
                <el-form-item label="资产代码" style="margin-left: 60px">
                    <el-input v-model="form.zcdm" placeholder="资产代码" style="width: 220px"></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="类型名称">
                    <el-select v-model="form.lxbm" placeholder="类型名称">
                        <el-option
                                :label="item.deviceCatagoryName"
                                :value="item.deviceCatagoryCode"
                                v-for='item in form.sbfl'
                                :key='item.deviceCatagoryCode'
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="备注" style="margin-left: 70px">
                    <el-input v-model="form.bz" placeholder="备注" style="width: 220px"></el-input>
                </el-form-item>
            </div>
        </el-form>
    </div>
    </div>
</template>
<script>
    import axios from "axios";
    import swal from 'sweetalert';

    export default {
        data() {
            return {
                formInline: {
                    user: '',
                    region: ''
                },
                form: {
                    sbmc: '',
                    sbdm: '',
                    imei: '',
                    zcdm: '',
                    lxbm: '',
                    sbfl: [],
                    sblx: '',
                    stations: [],
                    imeis: [],
                    jgbm: '',
                    bz: '',
                    fileList:[]
                },
            }
        },
        mounted() {
            this.getStations();
            this.getEquipKind();
        },
        methods: {
            onSubmit() {
                console.log('submit!');
            },
            pageStateSwitch(e) {
                this.$emit('changeStatus', e)
            },
            getStations() {
                axios(`${global.HOST}/commen/getStations`)
                    .then(res => {
                        console.log(res);
                        this.form.stations = res.data;
                    })
            },
            getEquipKind() {
                axios(`${global.HOST}/device/category`)
                    .then(res => {
                        console.log(res);
                        this.form.sbfl = res.data;
                    })
            },
            submitForm(form) {
                console.log(form)
                let lxmc = "";
                form.sbfl.forEach(function (element) {
                    if (element.deviceCatagoryCode === form.lxbm) {
                        lxmc = element.deviceCatagoryName;
                    }
                });

                if (form.jgbm == "" || form.imei == "") {
                    swal('提交失败', '必填字段为空', 'info')
                    return;
                } else {
                    axios.post(`${global.HOST}/device/insert?jgbm=${form.jgbm}&sbmc=${form.sbmc}
                            &zcdm=${form.zcdm}&sbdm=${form.sbdm}&lxbm=${form.lxbm}&lxmc=${lxmc}&bz=${form.bz}&imei=${form.imei}`)
                        .then((res) => {
                            console.log(res)
                            res = res.data
                            if (res.code == 'F') {
                                swal('新增设备失败', res.message, 'error');
                            } else {
                                swal('新增设备成功', res.message, 'success');
                            }
                        });
                }

            },
            totalStateSwitch(state) {
                console.log(1);
                console.log(state);
                this.totalState = state;
            },

        }
    }
</script>

<style scoped>
    .el-col-25 {
        width: 20%;
    }

    p, h1, h2, h3, h4, h5, h6 {
        margin: 0;
        padding: 0;
    }

    .content1 {
        padding-top: 10px;
        padding-bottom: 10px;
        background-color: #f2f4f5;
    }

    .userWrap {
        width: 100%;
        height: 60px;
        background-color: rgb(255, 255, 255);
        padding: 15px 0;
        box-shadow: 0 5px 5px rgba(0, 0, 0, 0.05);
    }

    .back {
        height: 20px;
        width: 20px;
        background-color: rgb(199, 203, 210);
        border-radius: 50%;
        color: #ffffff;
        text-align: center;
        line-height: 20px;
        margin-left: 15px;
        margin-right: 15px;
        font-size: 18px;
        margin-top: 20px;
        display: block;
        float: left;
        cursor: pointer;
    }

    .userWrap img {
        display: block;
        float: left;
        width: 50px;
        height: 50px;
        margin-top: 5px;
    }

    .userInfo {
        width: calc(100% - 265px);
        border-right: 1px solid #dddddd;
        display: block;
        float: left;

    }

    .userInfo p {
        display: inline-block;
        padding: 0;
        margin: 0 25px;
        color: #999999;
    }

    .userInfoList {
        line-height: 30px;
    }

    .userInfoList span {
        color: #333333;
        margin-left: 8px;
    }

    .mensInfo {
        height: auto;
        background-color: #ffffff;
        padding: 15px;
        margin: 10px 15px;
    }

    .mensInfo h6 {
        text-align: center;
        color: #138bfc;
        font-weight: bold;
        margin-bottom: 15px;
    }

    .mensWrap {
        background-color: #ffffff;
        height: 80px;
        border-radius: 8px;
        box-shadow: 0 0 20px #e5e7ea;
    }

    .mensWrap .mensBg1 {
        background-color: #d7f1e2;
    }

    .mensWrap .menscolor1 {
        color: #4fb87c;
    }

    .mensWrap .mensBg2 {
        background-color: #f3e8d8;
    }

    .mensWrap .menscolor2 {
        color: #dba454;
    }

    .mensWrap .mensBg3 {
        background-color: #f5dede;
    }

    .mensWrap .menscolor3 {
        color: #ef6c6c;
    }

    .mensWrap .mensBg4 {
        background-color: #f0e3f8;
    }

    .mensWrap .menscolor4 {
        color: #c28ee0;
    }

    .mensWrap .mensBg5 {
        background-color: #cbeeee;
    }

    .mensWrap .menscolor5 {
        color: #3fb2b2;
    }

    .mens-header {
        position: relative;
        height: 40px;
        line-height: 40px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
    }

    .mens-header img {
        position: absolute;
        top: 5px;
        left: 5px;
        width: 30px;
        height: 30px;
    }

    .mens-header p {
        text-align: center;
        color: #666666;
        font-size: 14px;
    }

    .mens-con p.latitude,
    .mens-header p.latitude {
        width: 36%;
        display: block;
        float: left;
    }

    .mens-con p.latitude:first-of-type,
    .mens-header p.latitude:first-of-type {
        margin-left: 20%;
    }
    .tz-info {
        float: right;
        width: 79px;
        margin-right: 15px;
        height: 62px;
        background-color: #ecf6ff;
        border: 1px solid #76b3f3;
        border-radius: 6px;
        text-align: center;
        cursor: pointer;
    }
    .tz-info img {
        width: 32px;
        height: 32px;
        margin: 4px 23px 2px;
    }

    .tz-info h5 {
        font-weight: 500;
        font-size: 14px;
        color: #333333;
    }
    .mens-con {
        line-height: 40px;
        text-align: center;
        font-size: 20px;
    }

    .mens-con p {
        font-size: 20px;
    }

    .binding {
        height: 136px;
        border: 1px solid #94c8ff;
        background-color: #f5fbff;
        border-radius: 8px;
        box-shadow: 0 5px 5px #f2f2f2;
        margin-bottom: 10px;
    }

    .binding-top-left {
        height: 61px;
        margin: 13px 0;
    }

    .binding-top-left p {
        height: 30px;
        line-height: 30px;
    }

    .binding-top-left p em {
        color: #999999;
        padding-left: 20px;
        font-style: normal;
    }

    .binding-top-left p span {
        color: #333333;
        padding-left: 5px;
    }

    .binding-top-right {
        height: 87px;
        vertical-align: middle;
        text-align: center;
        line-height: 87px;
        color: #138bfc;
    }

    .binding-top-right img {
        vertical-align: middle;
        margin-right: 10px;
    }

    .binding-btm {
        text-align: center;
        height: 49px;
        background-color: #e5f5ff;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
        line-height: 49px;
        vertical-align: middle;
        color: #138bfc;
    }

    .binding-btm img {
        vertical-align: middle;
        padding-right: 10px;
        width: 20px;
        height: 20px;
    }

    .noBinding {
        margin: 10px 0;
        height: 50px;
        border: 1px solid #eeeeee;
        box-shadow: 0 5px 5px #f2f2f2;
        background-color: #fbfbfb;
        -webkit-border-radius: 8px;
        -moz-border-radius: 8px;
        border-radius: 8px;
        text-align: center;
        line-height: 50px;
        color: #000;
    }

    .noBinding-left {
        text-align: left;
        padding-left: 20px;
        color: #999999;
    }

    .noBinding span {
        padding-left: 5px;
        color: #666666;
    }

    .noBinding img {
        width: 20px;
        height: 20px;
        vertical-align: middle;
    }
</style>


