<template>
    <div>
        <div class="view">
            <div class="view-con">
                <el-row
                        :gutter="10"
                        class="view-title"
                >
                    <el-col :span="3">
                        <div class="equip-det">
                            <i
                                    class="el-icon-arrow-left back"
                                    @click="pageStateSwitch('userDes')"
                            />
                            <h5 class="view-name">
                                报警管理
                            </h5>
                        </div>
                    </el-col>
                    <el-col :span="5">
                        <img
                                src="../../assets/imgs/img-shoubiao.png"
                                alt=""
                                class="equip-img"
                        >
                        <span class="info-name">
设备名称:
</span>
                        <span class="info-det">
爱牵挂s3pro
</span>
                    </el-col>
                    <el-col :span="3">
                        <span class="info-name">
当前用户:
</span>
                        <span class="info-det">
徐美林
</span>
                    </el-col>
                    <el-col :span="5">
                        <span class="info-name">
床位/地址:
</span>
                        <span class="info-det">
厦门市湖底区名社区203室
</span>
                    </el-col>
                    <el-col :span="2">
                        <span class="info-name">
当前电量
</span>
                        <span class="info-det">
50%
</span>
                    </el-col>
                    <el-col :span="2">
                        <span class="info-name">
当前状态:
</span>
                        <span class="info-det">
在线
</span>
                    </el-col>
                </el-row>
                <div class="search">
                    <el-form
                            :inline="true"
                            :model="alarm"
                            class="demo-form-inline"
                    >
                        <span class="common-font">
预警状态
</span>
                        <el-radio-group
                                v-model="alarm.status"
                                size="small"
                                class="statusTab"
                        >
                            <el-radio-button label="0">
                                全部
                            </el-radio-button>
                            <el-radio-button label="1">
                                已处理
                            </el-radio-button>
                            <el-radio-button label="2">
                                未处理
                            </el-radio-button>
                        </el-radio-group>

                        <el-form-item
                                label="报警类型"
                                size="small"
                                class=""
                        >
                            <el-select
                                    v-model="alarm.type"
                                    placeholder="报警类型"
                                    class="alarmType"
                            >
                                <el-option
                                        label="全部"
                                        value="all"
                                />
                                <el-option
                                        label="体征信息"
                                        value="vital_sign"
                                />
                                <el-option
                                        label="超出围栏"
                                        value="fence"
                                />
                                <el-option
                                        label="SOS"
                                        value="sos"
                                />
                                <el-option
                                        label="设备异常"
                                        value="device"
                                />
                                <el-option
                                        label="跌倒"
                                        value="fall"
                                />
                            </el-select>
                        </el-form-item>
                        <span class="common-font">
报警时间
</span>
                        <el-date-picker
                                v-model="alarm.date"
                                size="small"
                                type="datetimerange"
                                end-placeholder="请选择日期"
                                start-placeholder="请选择日期"
                                :default-time="['12:00:00']"
                        />
                    </el-form>
                </div>
                <div class="alarmCollapse">
                    <el-collapse
                            accordion
                            class="collapse"
                    >
                        <el-collapse-item class="alarmItem" :key="i" v-for="(item,i) in list">
                            <template slot="title">
                                <el-row>
                                    <el-col :span="4">
                                        <!-- 未处理class是color-red,已处理:color-blue-->
                                        <span :class="[item.dqzt==='0'?'color-red':'color-blue']">
{{item.dqzt==='0'?'未处理':'已处理'}}
</span>
                                    </el-col>
                                    <el-col :span="4">
                                        <span class="color9">
运行状态:
</span>
                                        <!-- 异常class: color-red ,正常: color6-->
                                        <span class="color-red">
{{item.txmc}}
</span>
                                    </el-col>
                                    <el-col :span="6">
                                        <span class="color9">
报警时间:
</span>
                                        <span class="color6">
{{ item.ddsj }}
</span>
                                    </el-col>
                                    <el-col :span="6">
                                        <span class="color9">
报警类型:
</span>
                                        <span class="color6">
 {{ item.txmc }}
</span>
                                    </el-col>
                                </el-row>
                            </template>
                            <div class="alarmItemDet">
                                <el-row :gutter="20">
                                    <el-col :span="20">
                                        <el-form
                                                :model="alarm"
                                                class=""
                                                label-width="70px"
                                        >
                                            <!--<el-form-item-->
                                            <!--label="处理方式"-->
                                            <!--class="alarmLabel"-->
                                            <!--&gt;-->
                                            <!--<el-radio-group v-model="single.process">-->
                                            <!--<el-radio :label="0">-->
                                            <!--处理方式1-->
                                            <!--</el-radio>-->
                                            <!--<el-radio :label="1">-->
                                            <!--处理方式2-->
                                            <!--</el-radio>-->
                                            <!--<el-radio :label="2">-->
                                            <!--处理方式3-->
                                            <!--</el-radio>-->
                                            <!--<el-radio :label="3">-->
                                            <!--处理方式4-->
                                            <!--</el-radio>-->
                                            <!--</el-radio-group>-->
                                            <!--</el-form-item>-->
                                            <!--<el-form-item-->
                                            <!--label="引发原因"-->
                                            <!--class="alarmLabel"-->
                                            <!--&gt;-->
                                            <!--<el-radio-group v-model="single.cause">-->
                                            <!--<el-radio :label="0">-->
                                            <!--原因1-->
                                            <!--</el-radio>-->
                                            <!--<el-radio :label="1">-->
                                            <!--原因2-->
                                            <!--</el-radio>-->
                                            <!--<el-radio :label="2">-->
                                            <!--原因3-->
                                            <!--</el-radio>-->
                                            <!--<el-radio :label="3">-->
                                            <!--原因4-->
                                            <!--</el-radio>-->
                                            <!--</el-radio-group>-->
                                            <!--</el-form-item>-->
                                            <el-form-item
                                                    label="备注"
                                                    class="alarmLabel"
                                            >
                                                <el-input
                                                        v-model="handleContent[i]"
                                                        class="remark"
                                                        size="small"
                                                />
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                    <el-col :span="4">
                                        <el-button
                                                type="primary"
                                                plain
                                                size="small"
                                                class="process-btn"
                                                @click="handle(item.txlsh,i)"
                                        >
                                            保存
                                        </el-button>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-collapse-item>
                    </el-collapse>
                </div>

                <el-pagination
                        background
                        align="right"
                        v-if="totalCount>10"
                        layout="prev, pager, next"
                        :total="totalCount"
                        @current-change="query"
                />
            </div>
        </div>
    </div>
</template>

<style scoped>
    @import "../../assets/css/alarmManage.css";

</style>

<script>
    import axios from 'axios'
    import Swal from 'sweetalert2'
    import ElRow from "element-ui/packages/row/src/row";
    import ElCol from "element-ui/packages/col/src/col";
    import ElForm from "../../../node_modules/element-ui/packages/form/src/form.vue";
    import ElFormItem from "../../../node_modules/element-ui/packages/form/src/form-item.vue";
    import ElInput from "../../../node_modules/element-ui/packages/input/src/input.vue";
    import ElButton from "../../../node_modules/element-ui/packages/button/src/button.vue";

    export default {
        name: "AlarmManage",
        components: {
            ElButton,
            ElInput,
            ElFormItem,
            ElForm,
            ElCol,
            ElRow
//            ElButton,
//            ElForm,
//            ElFormItem,
//            ElCol,
//            ElRow
//            // 'v-header': header
        },
        data() {
            return {
                alarm: {
                    status: 0,  //enum :[1:'已处理',2:'未处理',0:'全部']
                    type: 'all',  //enum :['all','vital_sign', 'fence', 'sos', 'device', 'fall' ]
                    date: '',
                },
                single: {
                    // process: 0,
                    // cause: 0,
                    remark: ''
                },
                totalCount: 0,
                list: [],
                handleContent: []
            }
        },
        mounted() {
            this.query()
        },
        watch: {
            alarm: {
                handler() {
                    this.query()
                },
                deep: true
            },
        },
        methods: {
            pageStateSwitch(e) {
                console.log(e);
                this.$emit('changeStatus', e)
            },
            query(page = 1) {
                console.log(this.alarm);
                console.log(this.$parent.currentUser);

                const params = {
                    pageSize: 10,
                    pageNum: page,
                    status: this.alarm.status,
                    start: +this.alarm.date[0],
                    end: +this.alarm.date[1],
                    patientId: this.$parent.currentUser.patientid
                }
                !this.alarm.date && delete params.start && delete params.end

                console.log(params);
                axios.get(`${global.HOST}/alarm/list/${this.alarm.type}`, {params})
                    .then(res => {
                        console.log(res);
                        this.totalCount = res.data.total
                        this.list = res.data.rows
                        this.list.map(v => this.handleContent.push(v.dqbz))

                    })
            },
            handle(id, index) {
                if (!this.handleContent[index]) {
                    return Swal({
                        type: 'error',
                        title: '保存失败',
                        text: '保存内容不能为空',
                    })
                }


                axios.post(`${global.HOST}/alarm/handle/${id}`, {content: this.handleContent[index]}).then(res => {
                    // console.log(res);
                    if (res.data.code === 'T') {
                        Swal({
                            type: 'success',
                            title: '成功',
                            text: '处理内容保存成功',
                        }).then(this.query)
                    } else {
                        Swal({
                            type: 'success',
                            title: '失败',
                            text: '处理内容保存失败',
                        })
                    }
                })

            }
        },
    }
</script>



