<template>
    <div class="search">
        <el-form
:inline="true"
:model="formInline"
class="demo-form-inline"
>
            <span class="common-font">
预警状态
</span>
            <el-radio-group
v-model="formInline.currentStatus"
size="small"
>
                <el-radio-button label="0">
全部
</el-radio-button>
                <el-radio-button label="1">
已处理
</el-radio-button>
                <el-radio-button label="2">
未处理
</el-radio-button>
            </el-radio-group>

            <el-form-item
label="设备名称"
size="small"
>
                <el-input
v-model="formInline.region"
placeholder="设备名称"
/>
            </el-form-item>
            <el-form-item
label="绑定人"
size="small"
>
                <el-input
v-model="formInline.user"
placeholder="绑定人"
/>
            </el-form-item>

            <span class="common-font">
报警时间
</span>
            <el-date-picker
                    v-model="formInline.dataValue"
                    size="small"
                    type="datetimerange"
                    end-placeholder="结束日期"
                    start-placeholder="开始日期"
                    :default-time="['12:00:00']"
/>

            <!--<el-form-item>-->
            <!--<el-button type="primary" @click="onSubmit" size="small">查询</el-button>-->
            <!--</el-form-item>-->
        </el-form>
    </div>
</template>

<script>
    import Middleware from '../common/middleware.js'

    export default {
        name: "SearchInput",
        data() {
            return {

                formInline: {
                    user: '',
                    region: '',
                    dataValue: '',
                    currentStatus: 2, // default undo
                },
            }
        },
        watch: {
            // watching the object fromInline
            formInline: {
                handler() {
                    this.search()
                },
                deep: true
            }
        },
        methods: {
            onSubmit() {
                console.log('submit!');
            },
            search() {
                console.log(this.formInline.currentStatus);
                let [time, status, equip, user] = [this.formInline.dataValue, this.formInline.currentStatus, this.formInline.region, this.formInline.user]
                Middleware.$emit('conditionalChange', {time, status, equip, user})
            }
        }
    }
</script>

<style scoped>
    .search {
        width: 100%;
    }

    .demo-form-inline {
        /*margin-top: px;*/
        /*width: 94%;*/
        margin: 0 15px;
        min-height: 60px;
        line-height: 60px;
        background-color: #ffffff;
        padding-left: 15px;
    }

    .el-radio-group {
        margin-right: 30px;
    }

    .el-button {
        vertical-align: top;
    }

    .el-input {
        line-height: 60px;
    }

    .common-font {
        color: #666666;
        font-size: 12px;
        margin-right: 12px;
    }

    .el-radio-button__inner {
        color: #999999;
    }

</style>



