<template>
    <div class="content1">
        <div class="userWrap">
            <i class="el-icon-arrow-left back"
               @click="pageStateSwitch('list')"/>
            <img src="../../assets/imgs/img-shoubiao.png" alt="">
            <div class="userInfo">
                <el-row>
                    <el-col :span="20">
                    <el-row class="userInfoList">
                        <el-col :span="6"><p>名称:<span>{{ equipment.name }}</span></p></el-col>
                        <el-col :span="6"><p>类型:<span>{{ equipment.type }}</span></p></el-col>
                        <el-col :span="6"><p>IMEI:<span>{{ equipment.imei }}</span></p></el-col>
                        <el-col :span="6"><p>入库时间:<span>{{ equipment.saveTime }}</span></p></el-col>
                    </el-row>
                        <el-row class="userInfoList">
                            <el-col :span="6"><p>当前状态:<span>{{ equipment.status }}</span></p></el-col>
                            <el-col :span="6"><p>设备电量:<span>{{ equipment.electricity }}</span></p></el-col>
                            <el-col :span="6"><p>当前用户:<span>{{ equipment.user }}</span></p></el-col>
                        </el-row>
                    </el-col>
                    <el-col :span="4">
                        <div class="tz-info" @click="pageStateSwitch('alarmTactics')">
                            <img src="../../assets/imgs/icon-xtgzrzxx.png" alt="">
                            <h5>报警策略</h5>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <div style="display: block;float:left;margin-top: 15px;margin-left: 15px">
                <el-button type="danger" size="mini" @click="handleRemove(equipment)">
                    解绑
                </el-button>
                <el-button
                        type="primary"
                        size="mini"
                        @click="checkToDetail"
                >
                    详情
                </el-button>
            </div>
        </div>
        <div class="mensInfo" v-for="item in this.recordList" :key="item.zclsh">
            <h6>设备历史记录</h6>
            <div class="noBinding">
                <el-row>
                    <el-col :span="5">
                        <p class="noBinding-left">
                            时间:<span>{{item.crksj}}</span>
                        </p>
                    </el-col>
                    <el-col :span="5">
                        <p class="noBinding-left green">
                            类型:{{item.ywlb}}
                        </p>
                    </el-col>
                    <el-col :span="5">
                        <p class="noBinding-left">
                            <!--操作人员:<span>{{item.czry}}</span>-->
                            操作人员:<span>admin</span>
                        </p>
                    </el-col>
                    <el-col :span="4">
                        <p class="noBinding-left">
                            <!--操作人员:<span>{{item.czry}}</span>-->
                            绑定/解绑人:<span>{{item.xm}}</span>
                        </p>
                    </el-col>
                    <el-col :span="5">
                        <p class="noBinding-left">
                            使用者:<span>{{item.xm}}</span>
                        </p>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import swal from 'sweetalert';

    export default {
        name: "UserDes",
        props: {
            deviceId: {
                type: String,
                required: true
            }
        },
        data() {
            return {
                pageState: 'list',
                userDes: {},
                equipDes: {},
                equipment: {
                    name: '',
                    type: '',
                    imei: '',
                    status: '',
                    electricity: '',
                    user: '',
                    saveTime: ''
                },
                recordList: [],
            }
        },
        mounted() {
            this.query();
            this.getEquipRecord();
        },
        methods: {
            pageStateSwitch(e) {
                this.$emit('changeStatus', e)
            },
            query() {
                axios(`${global.HOST}/device/${this.deviceId}`)
                    .then(res => {
                        console.log(res);
                        res = res.data;
                        this.equipment.name = res.sbmc;
                        this.equipment.imei = res.imei;
                        this.equipment.type = res.lxmc;
                        this.equipment.saveTime = res.rksj;
                        this.equipment.yngrbsh = res.yngrbsh;
                        this.equipment.jgbm = res.jgbm;
                        this.equipment.zcdm = res.zcdm;
                        this.equipment.sbdm = res.sbdm;
                        this.equipment.lxbm = res.lxbm;
                        this.equipment.lxmc = res.lxmc;
                        this.equipment.zclsh = res.zclsh;
                        this.equipment.status = res.bdzt === '1' ? res.zxzt : '未绑定'; // 未绑定、在线、离线
                        // this.equipment.user = res.xt_djrygh; // todo 暂无绑定用户
                        //this.equipment.electricity = res.sbmc; // todo 暂无设备电量

                        Object.keys(this.equipment).map(v => this.equipment[v] || (this.equipment[v] = '暂无数据'))
                    })
            },
            getEquipRecord() {
                axios(`${global.HOST}/device/historyRecord?zclsh=${this.equipment.zclsh}&jgbm=${this.equipment.jgbm}`)
                    .then(res => {
                        console.log(res);
                        this.recordList = res.data
                    });
            },
            checkToDetail() {
                this.$emit('showDetail', 'viewEquipDetails')
            },
            handleRemove(equipment) {
                console.log(equipment)
                swal({
                    title: "解除绑定",
                    text: `您确定解除与设备${equipment.name}(IMEI编号${equipment.imei})的绑定关系吗？`,
                    buttons: ['取消', '确定']
                })
                    .then(willDelete => {
                        if (willDelete) {
                            axios.post(`${global.HOST}/device/${equipment.imei}/unbind/${equipment.yngrbsh}?jgbm=${equipment.jgbm}&sbmc=${equipment.name}
                            &zcdm=${equipment.zcdm}&sbdm=${equipment.sbdm}&lxbm=${equipment.lxbm}&lxmc=${equipment.lxmc}&zclsh=${equipment.zclsh}`)
                                .then((res) => {
                                    console.log(res)
                                    res = res.data
                                    let info = "";
                                    if (res.code == 'F') {
                                        info = "解除绑定失败";
                                    } else {
                                        info = "解除绑定成功";
                                    }
                                    swal(info, res.message, 'success')
                                        .then()
                                        .then(ok => {
                                            if (ok) {
                                                this.openFullScreen(2000);
                                                // this.$parent.queryUser(1)
                                                // setTimeout(() => {
                                                //     this.$parent.currentUser = this.$parent.userData.filter(v => v.id === this.currentUser.id)[0]
                                                //     this.queryType()
                                                // }, 3000)
                                            }
                                        })
                                })

                        }
                    });
            },
            openFullScreen(time) {
                const loading = this.$loading({
                    lock: true,
                    text: 'Loading',
                    spinner: 'el-icon-loading',
                    background: 'rgba(0, 0, 0, 0.7)'
                });
                setTimeout(() => {
                    loading.close();
                }, time);
            }


        }
    }
</script>


<style scoped>
    .el-col-25 {
        width: 20%;
    }

    p, h1, h2, h3, h4, h5, h6 {
        margin: 0;
        padding: 0;
    }

    .content1 {
        padding-top: 10px;
        padding-bottom: 10px;
        background-color: #f2f4f5;
    }

    .userWrap {
        width: 100%;
        height: 60px;
        background-color: rgb(255, 255, 255);
        padding: 15px 0;
        box-shadow: 0 5px 5px rgba(0, 0, 0, 0.05);
    }

    .back {
        height: 24px;
        width: 24px;
        background-color: rgb(199, 203, 210);
        border-radius: 50%;
        color: #ffffff;
        text-align: center;
        line-height: 24px;
        margin-left: 15px;
        margin-right: 15px;
        font-size: 18px;
        margin-top: 18px;
        display: block;
        float: left;
        cursor: pointer;
    }

    .userWrap img {
        display: block;
        float: left;
        width: 50px;
        height: 50px;
        margin-top: 5px;
    }

    .userInfo {
        width: calc(100% - 265px);
        border-right: 1px solid #dddddd;
        display: block;
        float: left;

    }

    .userInfo p {
        display: inline-block;
        padding: 0;
        margin: 0 25px;
        color: #999999;
    }

    .userInfoList {
        line-height: 30px;
    }

    .userInfoList span {
        color: #333333;
        margin-left: 8px;
    }

    .mensInfo {
        height: auto;
        background-color: #ffffff;
        padding: 15px;
        margin: 10px 15px;
    }

    .mensInfo h6 {
        text-align: center;
        color: #138bfc;
        font-weight: bold;
        margin-bottom: 15px;
    }

    .mensWrap {
        background-color: #ffffff;
        height: 80px;
        border-radius: 8px;
        box-shadow: 0 0 20px #e5e7ea;
    }

    .mensWrap .mensBg1 {
        background-color: #d7f1e2;
    }

    .mensWrap .menscolor1 {
        color: #4fb87c;
    }

    .mensWrap .mensBg2 {
        background-color: #f3e8d8;
    }

    .mensWrap .menscolor2 {
        color: #dba454;
    }

    .mensWrap .mensBg3 {
        background-color: #f5dede;
    }

    .mensWrap .menscolor3 {
        color: #ef6c6c;
    }

    .mensWrap .mensBg4 {
        background-color: #f0e3f8;
    }

    .mensWrap .menscolor4 {
        color: #c28ee0;
    }

    .mensWrap .mensBg5 {
        background-color: #cbeeee;
    }

    .mensWrap .menscolor5 {
        color: #3fb2b2;
    }

    .mens-header {
        position: relative;
        height: 40px;
        line-height: 40px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
    }

    .mens-header img {
        position: absolute;
        top: 5px;
        left: 5px;
        width: 30px;
        height: 30px;
    }

    .mens-header p {
        text-align: center;
        color: #666666;
        font-size: 14px;
    }

    .mens-con p.latitude,
    .mens-header p.latitude {
        width: 36%;
        display: block;
        float: left;
    }

    .mens-con p.latitude:first-of-type,
    .mens-header p.latitude:first-of-type {
        margin-left: 20%;
    }
    .tz-info {
        float: right;
        width: 79px;
        margin-right: 15px;
        height: 62px;
        background-color: #ecf6ff;
        border: 1px solid #76b3f3;
        border-radius: 6px;
        text-align: center;
        cursor: pointer;
    }
    .tz-info img {
        width: 32px;
        height: 32px;
        margin: 4px 23px 2px;
    }

    .tz-info h5 {
        font-weight: 500;
        font-size: 14px;
        color: #333333;
    }
    .mens-con {
        line-height: 40px;
        text-align: center;
        font-size: 20px;
    }

    .mens-con p {
        font-size: 20px;
    }

    .binding {
        height: 136px;
        border: 1px solid #94c8ff;
        background-color: #f5fbff;
        border-radius: 8px;
        box-shadow: 0 5px 5px #f2f2f2;
        margin-bottom: 10px;
    }

    .binding-top-left {
        height: 61px;
        margin: 13px 0;
    }

    .binding-top-left p {
        height: 30px;
        line-height: 30px;
    }

    .binding-top-left p em {
        color: #999999;
        padding-left: 20px;
        font-style: normal;
    }

    .binding-top-left p span {
        color: #333333;
        padding-left: 5px;
    }

    .binding-top-right {
        height: 87px;
        vertical-align: middle;
        text-align: center;
        line-height: 87px;
        color: #138bfc;
    }

    .binding-top-right img {
        vertical-align: middle;
        margin-right: 10px;
    }

    .binding-btm {
        text-align: center;
        height: 49px;
        background-color: #e5f5ff;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
        line-height: 49px;
        vertical-align: middle;
        color: #138bfc;
    }

    .binding-btm img {
        vertical-align: middle;
        padding-right: 10px;
        width: 20px;
        height: 20px;
    }

    .noBinding {
        margin: 10px 0;
        height: 50px;
        border: 1px solid #eeeeee;
        box-shadow: 0 5px 5px #f2f2f2;
        background-color: #fbfbfb;
        -webkit-border-radius: 8px;
        -moz-border-radius: 8px;
        border-radius: 8px;
        text-align: center;
        line-height: 50px;
        color: #000;
    }

    .noBinding-left {
        text-align: left;
        padding-left: 20px;
        color: #999999;
    }

    .noBinding span {
        padding-left: 5px;
        color: #666666;
    }

    .noBinding img {
        width: 20px;
        height: 20px;
        vertical-align: middle;
    }
</style>




